import * as PIXI from 'pixi.js'
import { Transformable } from '../extendable'

export class AnimatedVikingRainbow extends Transformable {
    uniforms = {
        time: Math.random() * 10240.0,
    }

    tickerFn: any

    constructor() {
        super()
        this.assets.push('./images/bridge.png')
        this.staticOrInstanced = 'instanced'
    }

    create() {
        const updateUniformValues = (delta: number) => {
            this.uniforms.time += delta
        }
        this.tickerFn = updateUniformValues
        PIXI.Ticker.shared.add(updateUniformValues)
        return this.createAsset()
    }

    destroy() {
        PIXI.Ticker.shared.remove(this.tickerFn)
    }

    update(delta: number, child: PIXI.Container) {
        super.update(delta, child)
    }

    createAsset() {
        const _container = new PIXI.Container()

        const pixelAnimationData = PIXI.Loader.shared.resources['pixel_animation']
        
        let pixelAnimationInstance: any
        if (pixelAnimationData.spritesheet) {
            pixelAnimationInstance = new PIXI.AnimatedSprite(pixelAnimationData.spritesheet.animations['idle_dude']);
        }
        pixelAnimationInstance.animationSpeed = 0.167 + Math.random()*0.01;
        pixelAnimationInstance.scale.set((Math.random() > 0.5 ? -1 : 1) / 5.0, 1.0 / 5.0);

        pixelAnimationInstance.anchor.set(0.5, 1.0)
        pixelAnimationInstance.gotoAndPlay(Math.floor(Math.random()*6))
        // pixelAnimationInstance.play()

        _container.addChild(pixelAnimationInstance)

        const portalFilter = new PIXI.Filter(
            undefined,
            PIXI.Loader.shared.resources.portal_frag.data,
            this.uniforms
        )
        portalFilter.autoFit = false
        portalFilter.padding = 10

        _container.filters = [portalFilter]

        return _container
    }
}