import { GenericAnimatedSprite } from '../extendable'
import scoreTemplate from '../templates/score'
import healthTemplate from '../templates/health'

import {
    ANIMATION_ATTACK,
    ANIMATION_DIE,
    ANIMATION_IDLE,
    ANIMATION_FLEE,
    ANIMATION_WALK
} from '../../constants/'

export class Squirrel extends GenericAnimatedSprite {
    constructor() {
        super()
        this.template.load(scoreTemplate)
        this.template.load(healthTemplate)
        this.unpackTemplate()
        this.assets.push('./images/animals/squirrel.png')
        this.sizeX = 32
        this.sizeY = 32
        this.frameLengths = [6, 6, 8, 4, 2, 4, 4]

        this.animationLUT[ANIMATION_ATTACK] = 3
        this.animationLUT[ANIMATION_DIE] = 6
        this.animationLUT[ANIMATION_IDLE] = 0
        this.animationLUT[ANIMATION_FLEE] = 5
        this.animationLUT[ANIMATION_WALK] = 2
    }
}
