import * as PIXI from 'pixi.js'
import type System from '../engine/lib/Core/System'
import type { Generic2DirectionalSprite, Generic8DirectionalSprite } from '../engine/prefabs/extendable'

const size = 16
const multiples = 3
const scale = 5
const offsetX = (size * multiples * scale * 0.5)

const maxMushrooms = 8

export default class MushroomManager {
    system: System
    constructor(system: System) {
        this.system = system
    }

    pickup(spriteIdx: number) {
        const eatIndicatorDOM = document.querySelector('.press-e') as HTMLElement
        eatIndicatorDOM.style.display = 'block'

        let i = this.system.demoManager.inventoryIndex
        this.system.demoManager.inventoryValues[i] = spriteIdx

        this.system.demoManager.inventoryIndex += 1
        this.system.demoManager.inventoryIndex %= maxMushrooms

        const mush = this.system.demoManager.inventoryMushrooms.getSpriteByIdx(spriteIdx) as PIXI.Sprite
        mush.position.set(size * 0.5 + (size * i) + 1, size * 0.5)
        mush.anchor.set(0.5, 0.5)
        mush.scale.set(3.0 / scale)

        if (this.system.demoManager.uiContainer.children[i] === undefined) {
        } else {
            this.system.demoManager.uiContainer.removeChildAt(i)
        }
        this.system.demoManager.uiContainer.addChild(mush)
        this.system.demoManager.uiContainer.setChildIndex(mush, i)
        console.log(this.system.demoManager.inventoryValues)



    }

    eat() {
        const eatIndicatorDOM = document.querySelector('.press-e') as HTMLElement
        eatIndicatorDOM.style.display = 'none'
        const stacks = this.system.demoManager.inventoryValues.filter(o => o === 6).length
        const stackIndex = this.system.demoManager.inventoryValues.findIndex(o=>o===6)
        console.log('eating', stacks)
        console.log('inventory values', this.system.demoManager.inventoryValues)

        if(this.system.demoManager.inventoryValues.filter(o=>o!==-1).length === 0) {
            return
        }

        setTimeout(()=>{
            this.system.renderManager.shaderManager.compositorUniforms.ambientLightStrength = 2.0
            this.system.renderManager.shaderManager.groundShadowFilterUniforms.watts = 0.3    
        },60)

        window.location.hash = this.system.demoManager.inventoryValues.filter(o=>o!==-1).join(',')

        const paddingOffsets = [
            { x: 0.0, y: 0.0 }, // crap, never used
            { x: 0.0, y: 0.15 },        // 1
            { x: 0.38, y: 0.38 },        // 2
            { x: 0.5, y: 0.25 },        // 3
            { x: 0.3125, y: 0.3125 },   // 4
            { x: 0.38, y: 0.38 },       // 5
            { x: 0.44, y: 0.44 },       // 6
            { x: 0.5, y: 0.25 },       // 7
            { x: 0.285, y: 0.285 },       // 8
        ]

        const paddingOffsetsStackIndex = [
            { x: 0.0, y: 0.0 }, // crap, never used
            // { x: 0.75, y: 0.75 },        // 1
            { x: 0.5, y: 0.5 },        // 1
            { x: 0.5, y: 0.25 },        // 2
            { x: 0.5, y: 0.25 },        // 3
            { x: 0.3125, y: 0.3125 },   // 4
            { x: 0.38, y: 0.38 },       // 5
            { x: 0.44, y: 0.44 },       // 6
            { x: 0.5, y: 0.25 },       // 7
            { x: 0.285, y: 0.285 },       // 8
        ]

        if (this.system.demoManager.inventoryValues.filter(o => o === -1).length === maxMushrooms) {
            return
        }
        const emission = this.system.renderManager.layerManager.assetLayers.get('emission')
        const entities = this.system.renderManager.layerManager.assetLayers.get('entities')

            ;['128'].forEach(key => {
                const pc_prefab = this.system.renderManager.instancedPrefabs.find(o => o.key === key)?.value as Generic2DirectionalSprite
                if(pc_prefab === undefined) {
                    return
                }
                console.log('pc_prefab', pc_prefab)
                console.log('STACKS', paddingOffsets[stacks], stacks, stackIndex)
                const mushroomFilters = pc_prefab.mushroomFilters
                const filters = pc_prefab.filters as PIXI.Filter[]
                pc_prefab.uniforms.stacks = stacks
                const pcContainer = entities?.getChildByName(key)
                if (pcContainer !== undefined && pcContainer !== null) {
                    emission?.addChild(pcContainer)
                }

                filters.length = 0

                this.system.demoManager.inventoryValues.forEach(value => {
                    if (value === -1) {
                        return
                    }
                    const mushroomFilterIdx = value % mushroomFilters.length
                    filters.push(mushroomFilters[mushroomFilterIdx])
                })

                // enable all the filters
                filters?.forEach(filter => { filter.enabled = true })
                console.log('filters', filters)
            })

        // restore the inventory state
        this.system.demoManager.inventoryIndex = 0
        this.system.demoManager.inventoryValues = [-1, -1, -1]

        // remove the UI elements
        console.log(this.system.demoManager.uiContainer.children)
        while (this.system.demoManager.uiContainer.children.length > 0) {
            this.system.demoManager.uiContainer.removeChildAt(0)
        }
    }
    wash() {
        setTimeout(()=>{
            this.system.renderManager.shaderManager.compositorUniforms.ambientLightStrength = 1.8
            this.system.renderManager.shaderManager.groundShadowFilterUniforms.watts = 2.09
        },60)

        // console.log('wash')
        const pc_prefab = this.system.renderManager.instancedPrefabs.find(o => o.key === '128')?.value as Generic8DirectionalSprite
        const filters = pc_prefab.filters
        filters?.forEach(filter => { filter.enabled = false })

        const emission = this.system.renderManager.layerManager.assetLayers.get('emission')
        const entities = this.system.renderManager.layerManager.assetLayers.get('entities')
        const pcContainer = emission?.getChildByName('128')
        if (pcContainer !== undefined && pcContainer !== null) {
            entities?.addChild(pcContainer)
        }

    }
}