import * as PIXI from 'pixi.js'
import { Transformable } from '../extendable'
import killableTemplate from '../templates/killable'

export class Coin extends Transformable {
    constructor() {
        super()
        this.template.load(killableTemplate)
    }

    create() {
        return this.convertSpriteSheet()
    }

    update(delta: number, child: PIXI.Container) {
        super.update(delta, child)
    }

    convertSpriteSheet() {

        const textureAtlas = PIXI.Texture.from(this.assets[0])

        // console.log(textureAtlas.width)

        const sizeX = 16
        const sizeY = 16

        const idleTextures: PIXI.Texture[] = []
        for (let i = 0; i < (textureAtlas.width / sizeX); i++) {
            idleTextures.push(
                new PIXI.Texture(
                    textureAtlas.castToBaseTexture(),
                    new PIXI.Rectangle(i * sizeX, 0, sizeX, sizeY)
                )
            )
        }

        const sprite = new PIXI.AnimatedSprite(idleTextures)
        sprite.animationSpeed = 1 / 8
        sprite.gotoAndPlay(Math.floor(Math.random() * textureAtlas.width / sizeX))
        sprite.anchor.set(0.5, 1.0)
        return sprite
    }
}