import * as PIXI from 'pixi.js'
import { Transformable } from '../extendable'

export class RainbowBridge extends Transformable {
    uniforms = {
        time: Math.random() * 10240.0,
        theta: 0.785
    }

    tickerFn: any

    constructor() {
        super()
        this.assets.push('./images/bridge.png')
        this.staticOrInstanced = 'instanced'
    }

    create() {
        const updateUniformValues = (delta: number) => {
            this.uniforms.time += delta
        }
        this.tickerFn = updateUniformValues
        PIXI.Ticker.shared.add(updateUniformValues)
        return this.createAsset()
    }

    destroy() {
        PIXI.Ticker.shared.remove(this.tickerFn)
    }

    update(delta: number, child: PIXI.Container) {
        super.update(delta, child)
    }

    createAsset() {
        const _container = new PIXI.Container()
        
        // console.log(
        //     this.assets[0],
        //     PIXI.Loader.shared.resources[this.assets[0]].data
        // )
        
        const rainbowBridge = new PIXI.Sprite(
            PIXI.Texture.from(
                PIXI.Loader.shared.resources[this.assets[0]].data
            )
        )

        const rainbowBridgeFilter = new PIXI.Filter(
            undefined, 
            PIXI.Loader.shared.resources.rainbow_bridge_frag.data, 
            this.uniforms
        )

        rainbowBridge.filters = [rainbowBridgeFilter]
        rainbowBridge.anchor.set(0.5)
        rainbowBridgeFilter.autoFit = false

        _container.addChild(rainbowBridge)
        
        return _container
    }
}