import Prefab from './Prefab'
import * as PREFABS from '../../prefabs/index'
import ids_to_prefabNames from '../../definitions/prefabs'

const PrefabMap: Map<string, Prefab> = new Map()
ids_to_prefabNames.forEach((tuple: { key: string, value: string }) => {
    //@ts-ignore
    if (PREFABS[tuple.value] === undefined) {
        throw new Error(`Prefab not found ${tuple.key} ${tuple.value}`)
    }

    //@ts-ignore
    const p = new PREFABS[tuple.value]()
    PrefabMap.set(tuple.key, p)
})

export default class PrefabManager {
    animatedPrefabs: Map<string, Prefab> = new Map()
    getPrefab(_class: string): Prefab {
        return PrefabMap.get(_class) as Prefab
    }
}