import * as PIXI from 'pixi.js'
import { Transformable } from '.'
import { UPSCALE_FACTOR } from '../../constants'

import animatedTemplate from '../templates/animated'
import nftIdTemplate from '../templates/nftId'

const availableNFTs = ["0023", "0059", "0067", "0082", "0102", "0104", "0113", "0118", "0134", "0159", "0161", "0179", "0180", "0183", "0206", "0213", "0224", "0228", "0256", "0268", "0272", "0288", "0307", "0309", "0338", "0342", "0361", "0362", "0374", "0391", "0393", "0430", "0445", "0450", "0452", "0455", "0468", "0477", "0484", "0495", "0497", "0498", "0502", "0524", "0563", "0582", "0593", "0602", "0620", "0628", "0687", "0699", "0700", "0702", "0705", "0721", "0723", "0737", "0760", "0765", "0779", "0791", "0829", "0835", "0865", "0882", "0886", "0899", "0909", "0917", "0924", "0929", "0934", "0962", "0977", "0983", "0989", "0996", "1009", "1022", "1049", "1055", "1056", "1060", "1061", "1103", "1140", "1169", "1201", "1210", "1211", "1215", "1218", "1219", "1236", "1243", "1244", "1249", "1278", "1281"]

export class NPC8D extends Transformable {
    sizeX: number = -1
    sizeY: number = -1
    frameLengths: number[] = []
    animationLUT: number[] = []

    loaded = false

    tickerFn: any

    constructor() {
        super()
        this.template.load(animatedTemplate)
        this.template.load(nftIdTemplate)
        this.unpackTemplate()
        this.staticOrInstanced = 'instanced'

        const nftId = availableNFTs[Math.floor(Math.random() * availableNFTs.length)]
        // console.log(this.attributes.find(o => o.key === 'nftId')?.value)
        // console.log(this.attributes.find(o => o.key === 'direction')?.value)

        // let nftId = String(this.attributes.find(o => o.key === 'nftId')?.value).padStart(4, '0')
        // console.log('nftId', nftId)
        this.assets.push(['./output-sheets/', nftId, '_show - idle.png'].join(''))
        this.assets.push(['./output-sheets/', nftId, '_show - jog.png'].join(''))
    }

    create() {
        // console.log('creating npc')
        // console.log(this.attributes.find(o => o.key === 'nftId')?.value)
        const container = this.convertSpriteSheet()
        return container
    }

    update(delta: number, child: PIXI.Container) {
        super.update(delta, child)
        let direction = Number(this.attributes.find(o => o.key === 'direction')?.value)
        // console.log('npc update called', direction)
        if (this.loaded) {
            //@ts-ignore
            child.children[0].children.forEach(c => { c.renderable = false })
            //@ts-ignore
            child.children[1].children.forEach(c => { c.renderable = false })

            if (direction < 8) {
                //@ts-ignore
                child.children[1].children[direction].renderable = true
            } else {
                //@ts-ignore
                child.children[0].children[direction - 8].renderable = true
            }

        }
    }

    convertSpriteSheet() {
        // add filters and enable them based on which mushroom is being used
        // increase the parameters if multiple of the same mushroom
        const _container = new PIXI.Container()

        _container.addChild(new PIXI.Container())
        _container.addChild(new PIXI.Container())

        const tint = 0xFFFFFF
        const scale = 1.0

        const size = 96.0
        const nFrames = [9, 8]
        const sheetWidth = [9, 8]

        const loaded: number[] = []

            ;[1, 0].forEach(async (n: number) => {
                const textureAtlas = PIXI.Texture.from(this.assets[n], {
                    resourceOptions: { autoLoad: false }
                })
                await textureAtlas.baseTexture.resource.load();
                const baseTexture = textureAtlas.castToBaseTexture()

                nFrames[n] = textureAtlas.width / size
                sheetWidth[n] = nFrames[n]

                const textures: PIXI.Texture[] = []
                for (let i = 0; i < nFrames[n] * 8; i++) {
                    const y = Math.floor(i / sheetWidth[n])
                    const x = i - (y * sheetWidth[n])
                    textures.push(new PIXI.Texture(baseTexture, new PIXI.Rectangle(x * size, y * size, size, size)))
                }

                for (let i = 0; i < 8; i++) {
                    const sprite = new PIXI.AnimatedSprite(textures.slice(i * nFrames[n], (i * nFrames[n]) + nFrames[n]))
                    sprite.animationSpeed = 1.5 / nFrames[n]
                    sprite.gotoAndPlay(Math.floor(Math.random()*sprite.totalFrames))
                    sprite.anchor.set(0.5, 0.85)
                    sprite.scale.set(scale)

                    sprite.tint = tint
                    sprite.renderable = true
                    const localContainer = _container.children[n] as PIXI.Container
                    localContainer.addChild(sprite)
                }
                // console.log('loaded')
                loaded.push(n)
                if (loaded.length === 2) {
                    this.loaded = true
                    this.update(0, _container)
                }
            })
        return _container
    }
}