import { GenericAnimatedSprite } from '../extendable'
import scoreTemplate from '../templates/score'
import healthTemplate from '../templates/health'

import {
    ANIMATION_ATTACK,
    ANIMATION_DIE,
    ANIMATION_IDLE,
    ANIMATION_FLEE,
    ANIMATION_WALK
} from '../../constants/'

export class Fox extends GenericAnimatedSprite {
    constructor() {
        super()
        this.template.load(scoreTemplate)
        this.template.load(healthTemplate)
        this.unpackTemplate()
        this.assets.push('./images/animals/fox.png')
        this.sizeX = 32
        this.sizeY = 32
        this.frameLengths = [5, 14, 8, 11, 5, 6, 7]

        this.animationLUT[ANIMATION_ATTACK] = 3
        this.animationLUT[ANIMATION_DIE] = 6
        this.animationLUT[ANIMATION_IDLE] = 1
        this.animationLUT[ANIMATION_FLEE] = 4
        this.animationLUT[ANIMATION_WALK] = 2        
    }

}
