import * as PIXI from 'pixi.js'
import { Transformable } from '../extendable'

export class Portal extends Transformable {
    uniforms = {
        time: Math.random() * 10240.0,
    }

    tickerFn: any

    constructor() {
        super()
        this.assets.push('./images/bridge.png')
        this.staticOrInstanced = 'instanced'
    }

    create() {
        const updateUniformValues = (delta: number) => {
            this.uniforms.time += delta
        }
        this.tickerFn = updateUniformValues
        PIXI.Ticker.shared.add(updateUniformValues)
        return this.createAsset()
    }

    destroy() {
        PIXI.Ticker.shared.remove(this.tickerFn)
    }

    update(delta: number, child: PIXI.Container) {
        super.update(delta, child)
    }

    createAsset() {
        const _container = new PIXI.Container()

        const w = 200
        const h = 200

        const g = new PIXI.Graphics()
        g.beginFill(0x0000FF)
        g.drawRect(-w*0.5,-h*0.5,w,h)
        g.endFill()
        g.beginFill(0xFF0000)
        g.drawEllipse(0, 0, 40, 70)
        g.endFill()

        _container.addChild(g)

        const portalFilter = new PIXI.Filter(
            undefined,
            PIXI.Loader.shared.resources.portal_frag.data,
            this.uniforms
        )
        portalFilter.autoFit = false
        portalFilter.padding = 0

        g.filters = [portalFilter]

        return _container
    }
}