import * as PIXI from 'pixi.js'

// @ts-ignore - This is necessary for some PIXI tool functionality.
window.PIXI = PIXI

export default function createApplication(options: any): PIXI.Application {

    PIXI.utils.skipHello()
    PIXI.settings.RENDER_OPTIONS.antialias = false
    PIXI.settings.SCALE_MODE = PIXI.SCALE_MODES.NEAREST
    PIXI.settings.MIPMAP_TEXTURES = PIXI.MIPMAP_MODES.OFF
    PIXI.settings.WRAP_MODE = PIXI.WRAP_MODES.MIRRORED_REPEAT

    const app = new PIXI.Application({
        antialias: false,
        autoDensity: false,
        backgroundColor: 0x46403b,
        sharedTicker: true,
        sharedLoader: true,
        resizeTo: window,
        // resolution: 1
    });
    document.querySelector('.game')?.appendChild(app.view)

    let resizeTimeout = setTimeout(() => { }, 0)

    window.addEventListener('resize', () => {
        clearTimeout(resizeTimeout)
        resizeTimeout = setTimeout(() => { window.location.reload() }, 500)
    })

    return app
}

