import * as PIXI from 'pixi.js'

import type System from '../Core/System'

import FastFont from './FastFont'
import PrefabManager from '../Prefab/PrefabManager'
import LayerManager from './LayerManager/LayerManager'
import ShaderManager from './ShaderManager/ShaderManager'
import TextureManager from '../Rendering/TextureManager'
import Prefab from '../Prefab/Prefab'
import EventManager from '../Core/EventManager'
import { BasicPrefab } from '../Prefab/BasicPrefab'
import transformable from '../../prefabs/templates/transformable'

export default class RenderManager {
    system: System
    layerManager: LayerManager
    prefabManager: PrefabManager = new PrefabManager()
    shaderManager: ShaderManager
    textureManager: TextureManager
    // fastFont: FastFont

    frameCount = 0

    instancedPrefabs: { key: string, value: Prefab }[] = []     // todo, shouldn't this be a map?
    objectReferences: Map<string, PIXI.Container> = new Map<string, PIXI.Container>()
    basePrefab: Prefab = new BasicPrefab()  // this is a tiny prefab used to pull the class and id out of the template, todo - expand it to include the position

    extractBufferX: ArrayBuffer = new ArrayBuffer(0)
    extractPixelsX: Uint8Array = new Uint8Array()
    extractBufferY: ArrayBuffer = new ArrayBuffer(0)
    extractPixelsY: Uint8Array = new Uint8Array()

    constructor(system: System) {
        this.system = system
        // this.fastFont = new FastFont(this.system.app)
        this.textureManager = new TextureManager(this.system)
        this.layerManager = new LayerManager(this.system)
        this.shaderManager = new ShaderManager(this.system)
        this.basePrefab.template.load(transformable)
        this.basePrefab.unpackTemplate()
    }

    initialize() {
        console.log('intializing render manager')
        this.layerManager.initialize()
        this.layerManager.addScreenToStage()
        this.shaderManager.initialize()

        this.extractBufferX = new ArrayBuffer(this.textureManager.dimensions.x * 4)
        this.extractBufferY = new ArrayBuffer(this.textureManager.dimensions.y * 4)

        this.extractPixelsX = new Uint8Array(this.extractBufferX)
        this.extractPixelsY = new Uint8Array(this.extractBufferY)

        // EventManager.on('hits', event => {
        //     event.forEach((hit: any) => {
        //         const px = this.system.world._view[(Number(hit.key)) + 2] + ((Math.random() * 30) - 15)  // todo unpack the position using the basic prefab
        //         const py = this.system.world._view[(Number(hit.key)) + 3] - 10
        //         this.layerManager.hits.addHit(
        //             hit.value,
        //             px, py,
        //             0xFF0000
        //         )
        //     })
        // })
    }

    doRenderPipeline() {
        this.layerManager.cull()
        // this.layerManager.tickPools()    // TODO, fix

        this.shaderManager.tickUniforms()

        // assets
        this.renderLayerToTexture(this.layerManager.assetLayers.get('background-level'), this.textureManager.renderTargets.get('background'), true)
        this.renderLayerToTexture(this.layerManager.assetLayers.get('background-ground-objects'), this.textureManager.renderTargets.get('background'), false)
        this.renderLayerToTexture(this.layerManager.assetLayers.get('entities'), this.textureManager.renderTargets.get('entity'), true)
        this.renderLayerToTexture(this.layerManager.assetLayers.get('emission'), this.textureManager.renderTargets.get('emission'), true)

        // get the pc prefab from the emission layer, put it in the entities layer
        // render the special frame, see what comes out
        const pc = this.layerManager.assetLayers.get('emission')?.getChildByName('128')
        if (pc !== null && pc !== undefined) {
            this.layerManager.assetLayers.get('entities')?.addChild(pc)
            this.layerManager.sort()
            this.renderLayerToTexture(this.layerManager.assetLayers.get('entities'), this.textureManager.renderTargets.get('emission-entities-mask'), true)
            this.layerManager.assetLayers.get('emission')?.addChild(pc)
        }

        // non color assets, future use
        // this.renderLayerToTexture(this.layerManager.assetLayers.get('normal'), this.textureManager.renderTargets.get('normal'), true)
        // this.renderLayerToTexture(this.layerManager.assetLayers.get('reflectance'), this.textureManager.renderTargets.get('reflectance'), true)
        // this.renderLayerToTexture(this.layerManager.assetLayers.get('specular'), this.textureManager.renderTargets.get('specular'), true)
        // this.renderLayerToTexture(this.layerManager.assetLayers.get('transmission'), this.textureManager.renderTargets.get('transmission'), true)

        // compute        
        if (this.frameCount % 2 === 0) {
            this.renderLayerToTexture(this.layerManager.computeLayers.get('find-lights-x'), this.textureManager.renderTargets.get('lights-compute-x'), true)
            this.renderLayerToTexture(this.layerManager.computeLayers.get('find-lights-y'), this.textureManager.renderTargets.get('lights-compute-y'), true)
            this.renderLayerToTexture(this.layerManager.computeLayers.get('find-lights-reducer'), this.textureManager.renderTargets.get('lights-compute-reducer'), true)
        }

        // effects
        this.renderLayerToTexture(this.layerManager.effectsLayers.get('ground-shadow'), this.textureManager.renderTargets.get('shadow'), true)
        this.renderLayerToTexture(this.layerManager.effectsLayers.get('fog'), this.textureManager.renderTargets.get('fog'), true)
        this.renderLayerToTexture(this.layerManager.effectsLayers.get('lights'), this.textureManager.renderTargets.get('lights'), true)
        this.renderLayerToTexture(this.layerManager.effectsLayers.get('darkness'), this.textureManager.renderTargets.get('darkness'), true)

        // compositing
        this.renderLayerToTexture(this.layerManager.compositedLayers.get('render-screen'), this.textureManager.renderTargets.get('render'), true)
        this.renderLayerToTexture(this.layerManager.assetLayers.get('overlay'), this.textureManager.renderTargets.get('render'), false)

        this.frameCount += 1

    }

    renderLayerToTexture(layer: PIXI.Container | undefined, renderTexture: PIXI.RenderTexture | undefined, clear: boolean) {
        if (layer === undefined || renderTexture === undefined) {
            console.log('returning because of undefined layer or texture', layer, renderTexture)
            return
        }
        this.system.app.renderer.render(layer, { renderTexture, clear })
    }

}
