import * as PIXI from 'pixi.js'

import type { TiledLevelObject } from './TiledManager'

export default function TiledFixer(assetURL: string, levelObject: TiledLevelObject) {
    // console.log(assetURL)
    if (assetURL !== undefined) {
        return new PIXI.Sprite(PIXI.Texture.from(PIXI.Loader.shared.resources[`./images/Tiled_village/${assetURL}`].data))
    } else {
        const spriteSheetIdx = Number(levelObject.gid) - 1275

        const y = Math.floor(spriteSheetIdx / 10)
        const x = spriteSheetIdx - (y * 10)

        const texture = PIXI.Texture.from('./images/Tiled_village/tiles/dock/dock_tileset.png')
        const baseTexture = texture.castToBaseTexture()
        return new PIXI.Sprite(new PIXI.Texture(baseTexture, new PIXI.Rectangle(x * 64, y * 64, 64, 64)))
    }
}