/* eslint-disable react/jsx-no-target-blank */
import { useEffect, useState } from 'react'
import isMobile from 'ismobilejs'

import './Game.css';

import engine from '../../engine';
import MobileLandingView from '../../views/MobileLandingView';
import UIView from '../../views/UIView';


function Game() {
    const buildInfo = 'Build: v0.12.00-8D'
    const [mobileResult, setMobileResult] = useState<boolean>()

    useEffect(() => {
        const isMobileResult = isMobile(window.navigator);
        setMobileResult(isMobileResult.any)
    }, [])

    useEffect(() => {
        if (mobileResult === true) console.log('this is a mobile browser')
        else if (mobileResult === false) engine()
        else return
    }, [mobileResult])

    if (mobileResult === true) return <MobileLandingView />

    else if (mobileResult === false) return <>
        <div className="game" />
        <div className='loading'>Loading . . .</div>
        <div className='build-info'>{buildInfo}</div>
        <div className='loc'></div>
        <div className='loc press-e'>Press E to Eat</div>
        <div className='loc show-controls'>Press H for Help</div>
        <UIView />
    </>

    else return null
}

export default Game;
