import * as PIXI from 'pixi.js'
import type System from '../../Core/System'

export default class PBRAssetParser {
    system: System
    constructor(system: System) {
        this.system = system
    }
    parse(c: PIXI.Container | PIXI.Sprite | PIXI.AnimatedSprite): PIXI.DisplayObject[] {
        const dead = new PIXI.Container()

        const children: PIXI.DisplayObject[] = []

        while(c.children.length) {
            c.children.forEach((child: PIXI.DisplayObject)=>{
                const settings = JSON.parse(child.name)
                const layer = this.system.renderManager.layerManager.assetLayers.get(settings.layer)
                if(layer !== undefined) {                    
                    const _c = new PIXI.Container()
                    _c.name = child.name
                    _c.position.set(c.position.x, c.position.y)
                    _c.addChild(child)
                    children.push(_c)
                    layer.addChild(_c)
                } else {
                    console.log('layer not found')
                    dead.addChild(child)
                }
            })
        }
        return children
    }
}