import type System from '../Core/System'
import type NetworkManager from '../Network/NetworkManager'

import KeyboardManager from './KeyboardManager'
import MouseManager from './MouseManager'

export default class InteractionManager {
    keyboardManager: KeyboardManager = new KeyboardManager()
    mouseManager: MouseManager = new MouseManager()
    network: NetworkManager
    constructor(system: System) {
        this.network = system.networkManager
        this.keyEvent = this.keyEvent.bind(this)
        this.attachEvents()
    }

    attachEvents() {
        window.addEventListener('keydown', this.keyEvent)
        window.addEventListener('keyup', this.keyEvent)
    }

    keyEvent(event: KeyboardEvent) {        
        this.keyboardManager.handleEvent(event)

        // todo remove
        // hack movement for solo demo
        // this.network.socket.emit('keys', this.keyboardManager.pressedKeys)
    }

}