import * as PIXI from 'pixi.js'
import type System from '../Core/System'

import { UPSCALE_FACTOR } from '../../constants'

export default class TextureManager {
    system: System

    renderTargets: Map<string, PIXI.RenderTexture> = new Map()
    masks: Map<string, PIXI.Texture> = new Map()

    dimensions = { x: 0, y: 0 }

    constructor(system: System) {
        console.log('texture manager constructor')
        this.system = system
        
        // this rounds up to the next power of 2 number so the textures don't have a shitfit about not being pwoers of two
        let pow2Width = Math.pow(2, Math.ceil(Math.log(Math.ceil(this.system.app.view.width / UPSCALE_FACTOR)) / Math.log(2)))
        let pow2Height = Math.pow(2, Math.ceil(Math.log(Math.ceil(this.system.app.view.height / UPSCALE_FACTOR)) / Math.log(2)))
        
        this.dimensions.x = pow2Width 
        this.dimensions.y = pow2Height

        console.log('initializing texture manager', this.dimensions)

        // color
        this.renderTargets.set('background', PIXI.RenderTexture.create({ width: pow2Width, height: pow2Height }))
        this.renderTargets.set('entity', PIXI.RenderTexture.create({ width: pow2Width, height: pow2Height }))
        this.renderTargets.set('emission', PIXI.RenderTexture.create({ width: pow2Width, height: pow2Height }))
        
        // masks
        this.renderTargets.set('emission-entities-mask', PIXI.RenderTexture.create({ width: pow2Width, height: pow2Height }))

        // effects 
        this.renderTargets.set('shadow', PIXI.RenderTexture.create({ width: pow2Width, height: pow2Height }))
        this.renderTargets.set('fog', PIXI.RenderTexture.create({ width: pow2Width, height: pow2Height }))
        this.renderTargets.set('lights', PIXI.RenderTexture.create({ width: pow2Width, height: pow2Height }))
        this.renderTargets.set('darkness', PIXI.RenderTexture.create({ width: pow2Width, height: pow2Height }))
        
        // compute
        this.renderTargets.set('lights-compute-x', PIXI.RenderTexture.create({ width: pow2Width, height: 1 }))
        this.renderTargets.set('lights-compute-y', PIXI.RenderTexture.create({ width: 1, height: pow2Height }))
        this.renderTargets.set('lights-compute-reducer', PIXI.RenderTexture.create({ width: 1, height: 1 }))
        
        // non color
        // this.renderTargets.set('normal', PIXI.RenderTexture.create({ width: pow2Width, height: pow2Height }))       // unused
        // this.renderTargets.set('reflectance', PIXI.RenderTexture.create({ width: pow2Width, height: pow2Height }))  // unused       
        // this.renderTargets.set('specular', PIXI.RenderTexture.create({ width: pow2Width, height: pow2Height }))     // unused
        // this.renderTargets.set('transmission', PIXI.RenderTexture.create({ width: pow2Width, height: pow2Height })) // unused
        
        // output
        this.renderTargets.set('render', PIXI.RenderTexture.create({ width: pow2Width, height: pow2Height }))

    }
}