import * as PIXI from 'pixi.js'

import type LayerManager from './LayerManager'

class TestMesh extends PIXI.Mesh {
    constructor(texture: any, options: any) {
        const planeGeometry = new PIXI.PlaneGeometry(texture.width, texture.height, 2, 2);
        const meshMaterial = new PIXI.MeshMaterial(texture, options);
        super(planeGeometry, meshMaterial);
    }
}

export default function initialize(this: LayerManager) {
    console.log('initializing layer manager', this.system.renderManager.textureManager.dimensions)

    const baseFilterArea = new PIXI.Rectangle(
        0, 0,
        this.system.renderManager.textureManager.dimensions.x,
        this.system.renderManager.textureManager.dimensions.y
    )

    this.assetLayers.forEach((container: { filterArea: PIXI.Rectangle }) => {
        container.filterArea = baseFilterArea
    })

    this.compositedLayers.forEach((container: { filterArea: PIXI.Rectangle }) => {
        container.filterArea = baseFilterArea
    })

    this.effectsLayers.forEach((container: { filterArea: PIXI.Rectangle }) => {
        container.filterArea = baseFilterArea
    })

    this.computeLayers.forEach((container: { filterArea: PIXI.Rectangle }, name: string) => {
        if (name === 'find-lights-x') {
            container.filterArea = new PIXI.Rectangle(0, 0, this.system.renderManager.textureManager.dimensions.x, 1)
        } else if (name === 'find-lights-y') {
            container.filterArea = new PIXI.Rectangle(0, 0, 1, this.system.renderManager.textureManager.dimensions.y)
        } else if (name === 'find-lights-reducer') {
            container.filterArea = new PIXI.Rectangle(0, 0, 1, 1)
        }
    })

    // this.grassMaskTexture = PIXI.Texture.from(PIXI.Loader.shared.resources['village-mask-grass'].data)
    // this.grassMaskBuildingTexture = PIXI.Texture.from(PIXI.Loader.shared.resources['village-mask-grass-buildings'].data)
    this.waterMask = PIXI.Texture.from(PIXI.Loader.shared.resources['village-mask-water'].data)
    this.waterTexture = PIXI.Texture.from(PIXI.Loader.shared.resources['village-water'].data)

}