import * as PIXI from 'pixi.js'

import createApplication from '../../util/createApplication'
import AssetManager from './AssetManager'
import DemoManager from '../../../Demo/Level'
import InteractionManager from '../Interaction/InteractionManager'
import MetricsManager from '../Metrics/MetricsManager'
import NetworkManager from '../Network/NetworkManager'
import RenderManager from '../Rendering/RenderManager'

import World from '../World/World'

import getScreenRefreshRate from '../../util/determine-screen-refresh-rate'

class System {
    app: PIXI.Application
    assetManager: AssetManager = new AssetManager()
    demoManager: DemoManager
    interactionManager: InteractionManager
    metricsManager: MetricsManager
    networkManager: NetworkManager
    renderManager: RenderManager

    world: World

    // timeline stuff
    pos = { x: 2048, y: 1024 }

    constructor(options: any) {
        this.app = createApplication(options)
        this.metricsManager = new MetricsManager()
        this.world = new World()
        this.renderManager = new RenderManager(this)
        this.networkManager = new NetworkManager(this)
        this.interactionManager = new InteractionManager(this)
        this.demoManager = new DemoManager(this)

        this.tick = this.tick.bind(this)
        this.initialize = this.initialize.bind(this)

        // loading screen
        const loadingScreen = new PIXI.Container()
        loadingScreen.name = 'loading-screen'
        loadingScreen.position.set(this.app.view.width * 0.5, this.app.view.height * 0.5)

        const posterSprite = new PIXI.Sprite(PIXI.Texture.from('./images/ng-poster.png'))
        posterSprite.anchor.set(0.5, 0.5)
        loadingScreen.addChild(posterSprite)
        
        this.app.stage.addChild(loadingScreen)

        this.assetManager.loadAssets(() => {
            this.demoManager.tiledManager.initialize(this.initialize)
        })
    }

    initialize(): void {
        this.renderManager.initialize()
        this.demoManager.initialize()

        getScreenRefreshRate((fps: number) => {
            console.log('FPS', fps)

            if(fps > 100) {
                PIXI.Ticker.shared.maxFPS = 61
            }

            this.app.ticker.add(this.tick) // 
        }, false)
    

        // this.tick()  // this is for unbounded rendering mode

    }

    tick(delta: number): void {
        let _frameTime = window.performance.now()
        const nToUpdate = this.world._updatedEntitiesManager.count()
        const nToSkip = this.world._updatedEntitiesManager.countSkipped()

        const _worldTimeStart = window.performance.now()
        this.renderManager.layerManager.reconcile()
        const _worldTimeEnd = window.performance.now()

        this.metricsManager.updatePanels([
            _frameTime,
            _worldTimeEnd - _worldTimeStart,
            nToUpdate,
            nToSkip,
            this.networkManager.getBytesDown(),
            this.networkManager.getBytesUp(),
            this.networkManager.getPing()
        ])

        this.networkManager.clearStats()

        // TODO - measure the sort time
        if (nToUpdate > 0) {
        }
        this.renderManager.layerManager.sort()

        // TODO - measure the render time
        this.renderManager.doRenderPipeline()
        // setTimeout(this.tick) // this is for unbounded rendering mode
    }
}

export default System
