import * as PIXI from 'pixi.js'
import { Transformable } from '../extendable'

export class ProcedurallyGeneratedGround extends Transformable {
    uniforms = {
        time: Math.random() * 10240.0,
        uGrass: {},
        uDirt: {},
        dirtDimensions: { x: 0, y: 0 },
        grassDimensions: { x: 0, y: 0 },
        tileDimensions: { x: 0, y: 0 },
        d1: 0.0,
        multi1: { x: 1.0, y: 1.0 },
        d2: 0.0,
        theta: 0.0,
        multi2: { x: 1.0, y: 1.0 },
        sampleDistance: 0.01,
        sampleMulti: { x: 0.1, y: 0.1 },
        rotationDistance: 10.0
    }

    tickerFn: any

    constructor() {
        super()
        // this.assets.push('./images/northern-guilds.png')
        this.assets.push('./images/test_village/test-village-procedural-grass.png')
        this.assets.push('./images/test_village/test-village-procedural-dirt.png')
        // this.assets.push('./images/happy-cat.jpeg')
        // this.assets.push('./images/test-pattern-3.png')
        this.staticOrInstanced = 'instanced'

        this.createAsset = this.createAsset.bind(this)
    }

    create() {
        const updateUniformValues = (delta: number) => {
            this.uniforms.time += delta
        }
        this.tickerFn = updateUniformValues
        PIXI.Ticker.shared.add(updateUniformValues)
        return this.createAsset()
    }

    destroy() {
        PIXI.Ticker.shared.remove(this.tickerFn)
    }

    update(delta: number, child: PIXI.Container) {
        super.update(delta, child)
    }

    createAsset() {
        const _container = new PIXI.Container()

        const g = new PIXI.Graphics()

        const w = 512
        const h = 512

        g.beginFill(0xFF0000)
        g.drawRect(-w * 0.5, -h * 0.5, w, h)
        // g.beginFill(0x003300)
        // g.drawCircle(0,0,(w*0.25) + 2)
        // g.beginFill(0x007700)
        // g.drawCircle(0,0,(w*0.25) + 1.5)
        g.beginFill(0x00FF00)
        g.drawCircle(0,0,w*0.25)

        g.beginFill(0xFF0000)        
        g.drawRect(-w * 0.05, -h * 0.35, w * 0.1, h*0.45)

        const filterShift = new PIXI.Filter(undefined, PIXI.Loader.shared.resources.procedural_ground_frag.data, this.uniforms)
        filterShift.autoFit = false

        const filterEdge = new PIXI.Filter(undefined, PIXI.Loader.shared.resources.procedural_ground_edge_frag.data, this.uniforms)
        filterEdge.autoFit = false

        const filterColor = new PIXI.Filter(undefined, PIXI.Loader.shared.resources.procedural_ground_color_frag.data, this.uniforms)
        filterColor.autoFit = false

        //@ts-ignore - this is 
        const environmentFolder = window.gui.addFolder('Procedural Ground')
        environmentFolder.add(this.uniforms, 'd1', 0, 200).name('d1')
        environmentFolder.add(this.uniforms.multi1, 'x', 0, 10).name('multi1.x')
        environmentFolder.add(this.uniforms.multi1, 'y', 0, 10).name('multi1.y')
        environmentFolder.add(this.uniforms, 'd2', 0, 200).name('d2')
        environmentFolder.add(this.uniforms.multi2, 'x', 0, 10).name('multi2.x')
        environmentFolder.add(this.uniforms.multi2, 'y', 0, 10).name('multi2.y')
        environmentFolder.add(this.uniforms, 'sampleDistance', 0, 1.0).name('sample d')
        environmentFolder.add(this.uniforms.sampleMulti, 'x', 0.0, 1.0).name('sampleMulti.x')
        environmentFolder.add(this.uniforms.sampleMulti, 'y', 0.0, 1.0).name('sampleMulti.y')
        environmentFolder.add(this.uniforms, 'rotationDistance', 0.0, 100.0).name('rotationDistance')

        environmentFolder.open()

        const grassTex = PIXI.Texture.from(PIXI.Loader.shared.resources[this.assets[0]].data) as PIXI.Texture
        const dirtTex = PIXI.Texture.from(PIXI.Loader.shared.resources[this.assets[1]].data) as PIXI.Texture

        this.uniforms.uGrass = grassTex
        this.uniforms.uDirt = dirtTex

        this.uniforms.dirtDimensions = { x: dirtTex.width, y: dirtTex.height }
        this.uniforms.grassDimensions = { x: grassTex.width, y: grassTex.height }
        this.uniforms.tileDimensions = { x: w, y: h }

        _container.filters = [filterShift, filterEdge, filterColor]
        // _container.filters = [filterShift, filterEdge]
        // _container.filters = [filterEdge]
        // _container.filters = [filterColor]

        _container.addChild(g)

        return _container
    }
}