import { GenericAnimatedSprite } from '../extendable'
import killableTemplate from '../templates/killable'

export class Armadillo extends GenericAnimatedSprite {
    constructor() {
        super()
        this.template.load(killableTemplate)
        this.unpackTemplate()
        this.assets.push('./images/animals/armadillo.png')
        this.sizeX = 32
        this.sizeY = 32
        this.frameLengths = [7,4,7,3,3]
    }
}
