import { GenericAnimatedSprite } from '../extendable'
import scoreTemplate from '../templates/score'
import healthTemplate from '../templates/health'

import {
    ANIMATION_ATTACK,
    ANIMATION_DIE,
    ANIMATION_IDLE,
    ANIMATION_FLEE,
    ANIMATION_WALK
} from '../../constants/'

export class Cobra extends GenericAnimatedSprite {
    constructor() {
        super()
        this.template.load(scoreTemplate)
        this.template.load(healthTemplate)
        this.unpackTemplate()
        this.assets.push('./images/animals/cobra.png')
        this.sizeX = 32
        this.sizeY = 32
        this.frameLengths = [8,8,6,4,6]

        this.animationLUT[ANIMATION_ATTACK] = 2
        this.animationLUT[ANIMATION_DIE] = 4
        this.animationLUT[ANIMATION_IDLE] = 0
        this.animationLUT[ANIMATION_FLEE] = 3
        this.animationLUT[ANIMATION_WALK] = 1        
    }
}
