import * as PIXI from 'pixi.js'
import Prefab from '../../lib/Prefab/Prefab'
import transformableTemplate from '../templates/transformable'

export class Transformable extends Prefab {
    constructor() {
        super()
        this.template.load(transformableTemplate)
    }

    create(): PIXI.Container { return new PIXI.Container() }

    update(delta: number, child: PIXI.Container) {
        let newPosX = this.attributes.find(o => o.key === 'pos_x')?.value
        let newPosY = this.attributes.find(o => o.key === 'pos_y')?.value
        child.position.set(newPosX, newPosY)
    }
}
