import * as PIXI from 'pixi.js'
import { Transformable } from '../extendable'

export class Windmill extends Transformable {
    constructor() {
        super()
        this.assets.push('/images/Windmill.png')
    }

    create() {
        const _container = new PIXI.Container()
        const texture = PIXI.Texture.from(PIXI.Loader.shared.resources[this.assets[0]].data)
        // texture.baseTexture.mipmap = PIXI.MIPMAP_MODES.OFF
        const sprite = new PIXI.Sprite(texture)
        sprite.scale.set(0.1)
        sprite.anchor.set(0.5, 1.0)
        _container.addChild(sprite)
        return _container
    }

    update(delta: number, child: PIXI.Container) {
        super.update(delta, child)
    }
}