import * as PIXI from 'pixi.js'
import { Transformable } from './Transformable'
import healthTemplate from '../templates/health'

export class NPC extends Transformable {
    constructor() {
        super()
        this.template.load(healthTemplate)
    }

    update(delta: number, child: PIXI.Container) {
        super.update(delta, child)
    }
}