import * as PIXI from 'pixi.js'
import { Transformable } from '../extendable'

export class Slime extends Transformable {
    constructor() {
        super()
        this.assets.push('./images/slime-sheet.png')
    }

    create() {
        return this.convertSlime()
    }

    update(delta: number, child: PIXI.Container) {
        super.update(delta, child)
    }

    convertSlime() {

        const textureAtlas = PIXI.Texture.from(this.assets[0])

        const sizeX = 32
        const sizeY = 26

        const idleTextures: PIXI.Texture[] = []
        for (let i = 0; i < 8; i++) {
            idleTextures.push(
                new PIXI.Texture(
                    textureAtlas.castToBaseTexture(),
                    new PIXI.Rectangle(i * sizeX, 0, sizeX, sizeY)
                )
            )
        }

        const walkingTextures: PIXI.Texture[] = []
        for (let i = 0; i < 8; i++) {
            walkingTextures.push(
                new PIXI.Texture(
                    textureAtlas.castToBaseTexture(),
                    new PIXI.Rectangle(i * sizeX, 24, sizeX, sizeY)
                )
            )
        }

        const _container = new PIXI.Container()

        const sprite = new PIXI.AnimatedSprite(idleTextures)
        sprite.name = 'idle_animation'
        sprite.animationSpeed = 1.2 / 8
        sprite.play()
        sprite.anchor.set(0.5, 1.0)

        const spriteWalking = new PIXI.AnimatedSprite(walkingTextures)
        sprite.name = 'walking_animation'
        spriteWalking.animationSpeed = 1.2 / 8
        spriteWalking.play()
        spriteWalking.anchor.set(0.5, 1.0)

        spriteWalking.renderable = false

        _container.addChild(sprite)
        _container.addChild(spriteWalking)

        return _container
    }
}