import * as PIXI from 'pixi.js'
import type System from '../../Core/System'
import initializeShaderManager from './initializeShaderManager'
import * as dat from 'dat.gui'

const EMPTY_RENDER_TEXTURE = PIXI.RenderTexture.create({ width: 0, height: 0 })

const maskDimensions = { x: 4928, y: 4096 }

export default class ShaderManager {
    system: System
    initialize: Function

    constructor(system: System) {
        this.system = system

        const gui = new dat.GUI()
        gui.width = 300
        //@ts-ignore
        window.gui = gui
        // gui.hide()

        const environmentFolder = gui.addFolder('Environment Settings')
        environmentFolder.add(this.environmentVariables.windSpeed, 'x', -20, 20, 0.1).name('Wind Speed X').listen()
        environmentFolder.add(this.environmentVariables.windSpeed, 'y', -20, 20, 0.1).name('Wind Speed Y').listen()
        environmentFolder.open()



        const shadowFilterFolder = gui.addFolder('Lights & Shadow Settings')
        shadowFilterFolder.open()

        shadowFilterFolder.add(this.groundShadowFilterUniforms, 'watts', 0, 10)
        shadowFilterFolder.add(this.groundShadowFilterUniforms, 'shadowDistance', 1, 128)
        // shadowFilterFolder.add(this.groundShadowFilterUniforms, 'shadowDarkness', 0.001, 0.002)
        // shadowFilterFolder.add(this.groundShadowFilterUniforms, 'shadowFalloff', 1.0, 3.0)

        const fogFilterFolder = gui.addFolder('Fog')
        fogFilterFolder.open()
        fogFilterFolder.add(this.fogUniforms, 'thickness', 0, 4).name('Thickness')
        fogFilterFolder.add(this.fogUniforms, 'colorReductionSteps', 1, 64).name('Color Steps')
        fogFilterFolder.add(this.fogUniforms, 'sizeMultiplier', 1, 100).name('Size')

        const waterFilterFolder = gui.addFolder('Water')
        waterFilterFolder.open()
        waterFilterFolder.add(this.waterUniforms, 'timeMulti', 0, 10).name('Speed')
        waterFilterFolder.add(this.waterUniforms, 'distanceMulti', 0, 2).name('Amount')
        waterFilterFolder.add(this.waterUniforms, 'fadeFactor', 0, 2).name('Fade')
        waterFilterFolder.add(this.waterUniforms, 'fadeThreshold', 0, 2).name('Threshold')

        const mudFilterFolder = gui.addFolder('Procedural Grass & Mud')
        mudFilterFolder.open()
        mudFilterFolder.add(this.grassUniformsBackground, 'proceduralMudDistance', 0, 1000).name('Sample Distance')
        mudFilterFolder.add(this.grassUniformsBackground, 'proceduralMudSize', 0, .02).name('Sample Scale')

        const windOnGrassFolder = gui.addFolder('Fluttering in the Wind')
        windOnGrassFolder.open()
        windOnGrassFolder.add(this.grassUniformsBackground, 'timeMulti', 0, 2, 0.01).name('Wind Speed')

        const compositorFolder = gui.addFolder('Compositor')
        compositorFolder.open()
        compositorFolder.add(this.compositorUniforms, 'emissionStrength', 0.0, 30).name('Emissive')
        compositorFolder.add(this.compositorUniforms, 'glowStrength', 0.0, 30).name('Glow')
        compositorFolder.add(this.compositorUniforms, 'ambientLightStrength', 0.0, 5).name('Ambient')

        this.initialize = initializeShaderManager.bind(this)

    }

    environmentVariables = {
        windSpeed: { x: 1, y: -1 },
        windPosition: { x: -2000, y: 0 }
    }

    fogUniforms = {
        time: 0.1,
        windTime: { x: -1000.0, y: 0.0 },
        dimensions: { x: window.innerWidth, y: window.innerHeight },
        pixelSize: 1,
        pixelSizeReducer: 1,
        sizeMultiplier: 6.1,
        colorReductionSteps: 14,
        thickness: 1.0,
        windSpeed: { x: 0, y: 0 },
    }

    godRaysUniforms = {
        time: 0.0,
        bend: 0.5
    }

    rgbGlitchUniforms = {
        offset: Math.random() * 0.001
    }

    oldTVUniforms = {
        screenDimensions: { x: -1, y: -1 },
        time: 0.0
    }

    groundShadowFilterUniforms = {
        uEntities: {},
        screenDimensions: { x: window.innerWidth / 3.0, y: window.innerHeight / 3.0 },
        textureDimensions: { x: -1, y: -1 },
        watts: 1.8,
        shadowDistance: 72,
        wattsMulti: 1.0,
        shadowDarkness: 0.002,
        shadowFalloff: 1.0,
        lampPosition: { x: 0.5, y: 0.5 },
        uLampPosition: PIXI.Texture.EMPTY,
        lampColor: { x: 1.0, y: 1.0, z: 1.0 }
    }

    lightsFilterUniforms = {
        uEmission: {},
        screenDimensions: { x: window.innerWidth / 3.0, y: window.innerHeight / 3.0 },
        textureDimensions: { x: -1, y: -1 },
        blurSize: 10,
        sigma: 2.0,
        texOffset: { x: 0.0, y: 0.0 }
    }

    // grass on the ground
    grassUniformsBackground = {
        uGrassMask: {},
        renderDimensions: { x: -1, y: -1 },
        maskDimensions,
        offset: { x: 0, y: 0 },
        noiseSizeMultiplier: { x: 6.0, y: 1.0 },
        baseOffset: { x: 0, y: 0 },
        timeMulti: 0.05,
        time: 0.0,
        noiseSamplePosition: { x: 0, y: 0 },
        proceduralMudDistance: 676,
        proceduralMudSize: 0.0026,
        windSpeed: { x: 0, y: 0 }
    }

    // house roofs and leaves
    grassUniformsForeground = {
        uGrassMask: {},
        renderDimensions: { x: -1, y: -1 },
        maskDimensions,
        offset: { x: 0, y: 0 },
        noiseSizeMultiplier: { x: 150.0, y: 40.0 },
        baseOffset: { x: 20240.0, y: 0 },
        timeMulti: 0.87,
        time: 0.0,
        noiseSamplePosition: { x: 0, y: 0 },
        proceduralMudDistance: 0.0,
        proceduralMudSize: 0.0,
        factor: 5,
        windSpeed: { x: 0, y: 0 }
    }

    waterUniforms = {
        uWaterMask: {},
        uWaterTexture: {},
        renderDimensions: { x: -1, y: -1 },
        maskDimensions,
        offset: { x: 0, y: 0 },
        baseOffset: { x: 0, y: 0 },
        timeMulti: 1.14,
        noiseSamplePosition: { x: 0, y: 0 },
        time: 0,
        distanceMulti: 0.17,
        fadeFactor: 0.26,
        fadeThreshold: 0.3
    }

    compositorUniforms = {
        uEntities: EMPTY_RENDER_TEXTURE,
        uEmission: EMPTY_RENDER_TEXTURE,
        uBackground: EMPTY_RENDER_TEXTURE,
        uShadow: EMPTY_RENDER_TEXTURE,
        uFog: EMPTY_RENDER_TEXTURE,
        uLights: EMPTY_RENDER_TEXTURE,
        uDarkness: EMPTY_RENDER_TEXTURE,
        uEmissionMask: EMPTY_RENDER_TEXTURE,
        emissionStrength: 1.2,
        ambientLightStrength: 2.09,
        glowStrength: 4.0,
        _lampColor: { r: 0.1, g: 0.1, b: 0.1 }
    }

    tickUniforms(): void {

        // vary the windspeed
        // const windNoise = noise(this.environmentVariables.windPosition.x, this.environmentVariables.windPosition.y)
        // const windNoiseY = noise(this.environmentVariables.windPosition.x + 1024.0, this.environmentVariables.windPosition.y)

        // const multi = 0.01
        // this.environmentVariables.windSpeed.x += (windNoise - 0.5) * multi
        // this.environmentVariables.windSpeed.y += (windNoiseY - 0.5) * multi

        // increment the wind position from the wind velocity
        this.environmentVariables.windPosition.x += this.environmentVariables.windSpeed.x
        this.environmentVariables.windPosition.y += this.environmentVariables.windSpeed.y

        // TODO this information should come from the server and bet set as some kind of event
        this.fogUniforms.windTime.x = this.environmentVariables.windPosition.x + (this.system.renderManager.layerManager.offset.x * 2.0)
        this.fogUniforms.windTime.y = this.environmentVariables.windPosition.y + (this.system.renderManager.layerManager.offset.y * 2.0)
        this.godRaysUniforms.time += .05
        this.rgbGlitchUniforms.offset = (Math.random() * 0.004) * (Math.random() > 0.5 ? -1 : 1)

        this.oldTVUniforms.screenDimensions.x = this.system.app.view.width
        this.oldTVUniforms.screenDimensions.y = this.system.app.view.height

        this.oldTVUniforms.time += 0.01

        this.grassUniformsBackground.offset.x = this.system.renderManager.layerManager.offset.x
        this.grassUniformsBackground.offset.y = this.system.renderManager.layerManager.offset.y
        this.grassUniformsBackground.noiseSamplePosition.x += this.environmentVariables.windSpeed.x * 0.01
        this.grassUniformsBackground.noiseSamplePosition.y += this.environmentVariables.windSpeed.y * 0.01
        this.grassUniformsBackground.windSpeed.x = this.environmentVariables.windSpeed.x
        this.grassUniformsBackground.windSpeed.y = this.environmentVariables.windSpeed.y
        this.grassUniformsBackground.time += 0.01

        this.waterUniforms.offset.x = this.system.renderManager.layerManager.offset.x
        this.waterUniforms.offset.y = this.system.renderManager.layerManager.offset.y
        this.waterUniforms.noiseSamplePosition.x += this.environmentVariables.windSpeed.x * 0.01
        this.waterUniforms.noiseSamplePosition.y += this.environmentVariables.windSpeed.y * 0.01
        this.waterUniforms.time += 0.01

        this.grassUniformsForeground.offset.x = this.system.renderManager.layerManager.offset.x
        this.grassUniformsForeground.offset.y = this.system.renderManager.layerManager.offset.y
        this.grassUniformsForeground.noiseSamplePosition.x += this.environmentVariables.windSpeed.x * 0.01
        this.grassUniformsForeground.noiseSamplePosition.y += this.environmentVariables.windSpeed.y * 0.01
        this.grassUniformsForeground.windSpeed.x = this.environmentVariables.windSpeed.x
        this.grassUniformsForeground.windSpeed.y = this.environmentVariables.windSpeed.y
        this.grassUniformsForeground.time += 0.01
    }

}