import {
    AGREED_UPON_BITS,
    bitMasks,
    ENTITY_LENGTH
} from '../../constants'

export default class Template {
    scratchPadBuffer: ArrayBuffer
    scratchPad: Uint16Array
    properties: IPropertyDescription[]

    constructor() {
        this.properties = []
        this.scratchPadBuffer = new ArrayBuffer(ENTITY_LENGTH * 2)
        this.scratchPad = new Uint16Array(this.scratchPadBuffer)
    }

    load(properties: IPropertyDescription[]) {
        for (let i = 0; i < properties.length; i++) {
            this.properties.push(properties[i])
        }
    }

    addProperty(property: IPropertyDescription) {
        this.properties.push(property)
    }

    size(): number {
        return this.properties.reduce((o, _o) => _o.bits + o, 0)
    }

    length(): number {
        return this.properties.length
    }

    packRaw(data: number[] | Uint16Array) {
        for (let i = 0; i < data.length; i++) {
            this.scratchPad[i] = data[i]
        }
    }

    pack(o: any) { // TODO, this is typed, figure it out
        let runningBits = 0
        let runningUint16s = 0
        let packValue = 0
        let distanceToEdge = 0

        this.properties.forEach(property => {
            distanceToEdge = (AGREED_UPON_BITS - ((runningBits + property.bits) % AGREED_UPON_BITS)) % AGREED_UPON_BITS
            packValue |= (o[property.key] << distanceToEdge)
            runningBits += property.bits
            this.scratchPad[runningUint16s] = packValue
            if (distanceToEdge === 0) {
                runningUint16s += 1
                packValue = 0
            }
        })
    }

    unpack() {
        // TODO - can we make this a fixed length? this is where a ton of churn is going to go
        const unpackedProperties: IPropertyValue[] = []
        let bounds = [0, 0]
        for (let i = 0; i < this.properties.length; i++) {
            bounds[1] = bounds[0] + this.properties[i].bits
            unpackedProperties.push({ key: this.properties[i].key, value: this.unpackFromBuffer(bounds) })
            bounds[0] = bounds[1]
        }
        return unpackedProperties
    }

    unpackFromBuffer(bounds: number[]): number {
        const bitLength = bounds[1] - bounds[0]
        const bytesToSampleRange = [Math.floor(bounds[0] / AGREED_UPON_BITS), Math.floor(bounds[1] / AGREED_UPON_BITS)]
        for (let i = 0; i <= bytesToSampleRange[1] - bytesToSampleRange[0]; i++) {
            // todo, when we support crossing the boundary this is where it will happen
        }
        const distanceToEdge = (AGREED_UPON_BITS - (bounds[1] % AGREED_UPON_BITS)) % AGREED_UPON_BITS
        const mask = bitMasks[bitLength] << distanceToEdge;
        return (this.scratchPad[bytesToSampleRange[0]] & mask) >> distanceToEdge

        // when things break, this is how you look at what's going on
        // const v = (this.scratchPad[bytesToSampleRange[0]] & mask)
        // const shiftedV = v >> distanceToEdge
        // let bitString = this.scratchPad[bytesToSampleRange[0]].toString(2)
        // let maskString = mask.toString(2).padStart(bitString.length,'0')
        // if(bitString.length > maskString.length) {
        // maskString = mask.toString(2).padStart(bitString.length,'0')
        // } else {
        // bitString = this.scratchPad[bytesToSampleRange[0]].toString(2).padStart(maskString.length,'0')
        // }
        // console.log(['bits ', bitString, '\n', 'mask ', maskString].join(''))
        // console.log('bitLength', bitLength, 'distance', c, 'mask', mask.toString(2), 'value', v, 'shiftedV', shiftedV)
        // return shiftedV

        // bounds = [ 3, 10 ]
        //                 255 |                1024 |
        // 0000 0000 1111 1111 | 0000 0001 0000 0000 | 
        //    & &&&& &&, 7 bits to extract with a bitwise AND operation
        // 0000 0000 1100 0000
        //               >> 6, shift the distance to the right bit byte alignment

    }

}

export interface IPropertyDescription {
    key: string,
    bits: number
}

export interface IPropertyValue {
    key: string,
    value: number
}