import { Generic8DirectionalSprite } from '../extendable'
import killableTemplate from '../templates/killable'

export class PC extends Generic8DirectionalSprite {
    constructor() {
        super()
        this.template.load(killableTemplate)
        this.unpackTemplate()
        this.sizeX = 32
        this.sizeY = 32
        this.frameLengths = [6, 6, 6, 6, 6, 6, 6, 6]
    }
}
