import { DOMElement } from 'react'
import type System from '../engine/lib/Core/System'
import type DemoManager from './Level'

export default class DemoKeyEvents {
    system: System
    demo: DemoManager
    welcomeMessageActive = true

    debugSpawnIndex = 0
    vikingSpawnIndex = 0

    constructor(demo: DemoManager) {
        this.system = demo.system
        this.demo = demo

        // const controlsDOM = document.querySelector('.controls') as HTMLElement        
        const guiDOM = document.querySelector('.dg') as HTMLElement
        // const statsDOM = document.querySelector('.stats') as HTMLElement
        const eatIndicatorDOM = document.querySelector('.press-e') as HTMLElement
        // const faqDOM = document.querySelector('.faq') as HTMLElement

        // controlsDOM.style.display = 'none'
        // guiDOM.style.display = 'none'
        guiDOM.style.zIndex = '5000'

        // statsDOM.style.display = 'none'
        eatIndicatorDOM.style.display = 'none'
        // faqDOM.style.display = 'none'
    }

    initialize() {

        // const controlsDOM = document.querySelector('.controls') as HTMLElement
        const guiDOM = document.querySelector('.dg') as HTMLElement
        // const statsDOM = document.querySelector('.stats') as HTMLElement
        const buildInfoDOM = document.querySelector('.build-info') as HTMLElement
        const locDOM = document.querySelector('.loc') as HTMLElement
        const helpIndicatorDom = document.querySelector('.show-controls') as HTMLElement
        // const disclaimerDOM = document.querySelector('.disclaimer') as HTMLElement
        const eatIndicatorDOM = document.querySelector('.press-e') as HTMLElement
        // const faqDOM = document.querySelector('.faq') as HTMLElementdadsa

        document.addEventListener('keypress', (event) => {
            if (this.welcomeMessageActive) {
                this.welcomeMessageActive = false
                // the first movement clears the welcome message
                // disclaimerDOM.style.display = 'none'
            }

            if (event.code.indexOf('Digit') === 0) {
                const digitPressed = event.code.split('Digit')[1]
                console.log(parseInt(digitPressed))
                this.demo.mushroomManager.pickup(parseInt(digitPressed))
            } else {
                switch (event.key.toLowerCase()) {
                    case ']':
                        const spawn = this.demo.mushroomSpawns[this.debugSpawnIndex]
                        this.system.world.write(2, 2, [spawn.x, spawn.y])
                        this.debugSpawnIndex += 1
                        this.debugSpawnIndex %= this.demo.mushroomSpawns.length
                        break;
                    case '}':
                        const vikingPosition = this.demo.vikingPositions[this.vikingSpawnIndex]
                        this.system.world.write(2, 2, [vikingPosition.x - 20, vikingPosition.y])
                        this.vikingSpawnIndex += 1
                        this.vikingSpawnIndex %= this.demo.vikingPositions.length
                        break;
                    case 'l':
                        for (let i = 0; i < 8; i++) {
                            this.demo.mushroomManager.pickup(Math.floor(Math.random() * 10))
                        }
                        this.demo.mushroomManager.eat()
                        break;
                    case '_':
                        //@ts-ignore
                        window.toggleBuddy()
                        break;
                    case 'v':
                        this.demo.parentUIContainer.renderable = !this.demo.parentUIContainer.renderable
                        const displayStyle = this.demo.parentUIContainer.renderable ? 'block' : 'none'
                        buildInfoDOM.style.display = displayStyle
                        locDOM.style.display = displayStyle
                        helpIndicatorDom.style.display = displayStyle
                        break;
                    case 'c':
                        this.demo.mushroomManager.wash()
                        window.location.hash = ''
                        break;
                    case 'r':
                        this.system.renderManager.layerManager.debugViewmanager.debugViewContainer.renderable = !this.system.renderManager.layerManager.debugViewmanager.debugViewContainer.renderable
                        break;
                    case 'e':
                        this.demo.mushroomManager.eat()
                        break;
                    case 'u':
                        const guiShowing = guiDOM.style.display !== 'none'
                        if (guiShowing) {
                            guiDOM.style.display = 'none'
                        } else {
                            guiDOM.style.display = 'block'
                        }
                        break;
                    default:
                        break;
                }
            }
        })
    }

}