import * as PIXI from 'pixi.js'

import { UPSCALE_FACTOR } from '../../../constants'
import type System from '../../Core/System'

import initialize from './initializeLayerManager'
import DebugViewManager from './DebugViewManager'

import HitPool from '../Pools/HitPool'
import type FastFont from '../FastFont'

export default class LayerManager {
    system: System

    assetLayers: Map<string, PIXI.Container> = new Map()
    effectsLayers: Map<string, PIXI.Container> = new Map()
    computeLayers: Map<string, PIXI.Container> = new Map()
    compositedLayers: Map<string, PIXI.Container> = new Map()
    debugViewmanager: DebugViewManager

    screenSprite: PIXI.Sprite = new PIXI.Sprite(PIXI.Texture.EMPTY)

    dimensions = { x: 0, y: 0 }
    offset = { x: 0, y: 0 }

    grassMaskTexture: PIXI.Texture = PIXI.Texture.EMPTY
    grassMaskBuildingTexture: PIXI.Texture = PIXI.Texture.EMPTY
    waterMask: PIXI.Texture = PIXI.Texture.EMPTY
    waterTexture: PIXI.Texture = PIXI.Texture.EMPTY

    cullScreen: PIXI.Rectangle = new PIXI.Rectangle(0, 0, 0, 0)

    initialize: Function

    constructor(system: System) {
        console.log('layer manager constructor')
        this.system = system

        this.debugViewmanager = new DebugViewManager(this.system)

        // color layers
        this.assetLayers.set('background-level', new PIXI.Container())
        this.assetLayers.set('background-ground-objects', new PIXI.Container())
        this.assetLayers.set('entities', new PIXI.Container())
        this.assetLayers.set('emission', new PIXI.Container())

        // non-color layers
        this.assetLayers.set('normal', new PIXI.Container())
        this.assetLayers.set('reflectance', new PIXI.Container())
        this.assetLayers.set('specular', new PIXI.Container())
        this.assetLayers.set('transmission', new PIXI.Container())

        // effects
        this.effectsLayers.set('ground-shadow', new PIXI.Container())
        this.effectsLayers.set('building-and-entities-shadow', new PIXI.Container())
        this.effectsLayers.set('fog', new PIXI.Container())
        this.effectsLayers.set('lights', new PIXI.Container())
        this.effectsLayers.set('darkness', new PIXI.Container())
        this.effectsLayers.set('specular', new PIXI.Container())
        this.effectsLayers.set('reflectance', new PIXI.Container())

        // overlay stuff like quests
        const overlay = new PIXI.Container()
        overlay.renderable = false
        this.assetLayers.set('overlay', overlay)

        // compute
        this.computeLayers.set('find-lights-x', new PIXI.Container())
        this.computeLayers.set('find-lights-y', new PIXI.Container())
        this.computeLayers.set('find-lights-reducer', new PIXI.Container())

        // output layers
        this.compositedLayers.set('render-screen', new PIXI.Container())

        this.dimensions = {
            x: this.system.app.view.width,
            y: this.system.app.view.height
        }
        this.initialize = initialize.bind(this)
    }

    addScreenToStage() {
        console.log(this.system.renderManager)
        this.screenSprite = new PIXI.Sprite(this.system.renderManager.textureManager.renderTargets.get('render'))
        this.screenSprite.position.set(Math.floor(this.system.app.view.width * 0.5), Math.floor(this.system.app.view.height * 0.5))
        this.screenSprite.anchor.set(0.5, 0.5)
        this.screenSprite.scale.set(UPSCALE_FACTOR)
        this.system.app.stage.addChild(this.screenSprite)

        const crtFilter = new PIXI.Filter(undefined, PIXI.Loader.shared.resources.old_tv_frag.data, this.system.renderManager.shaderManager.oldTVUniforms)
        crtFilter.enabled = false
        this.screenSprite.filters = [crtFilter]

        this.debugViewmanager.initialize()
    }

    cull(): number {

        const padding = [300,300,300,300][UPSCALE_FACTOR-1] // ultra, high, medium, low
        this.cullScreen.x = -this.offset.x - padding
        this.cullScreen.y = -this.offset.y - padding
        this.cullScreen.width = this.system.renderManager.textureManager.dimensions.x + (padding*2)
        this.cullScreen.height = this.system.renderManager.textureManager.dimensions.y + (padding*2)

        let culled = 0
        const entities = this.assetLayers.get('entities')
        entities?.children.forEach((child, idx) => {
            if(child.name && child.name[0] === '{') {
                return
            }
            // todo -- take in to account the size of the child
            child.renderable = this.cullScreen.contains(child.position.x, child.position.y)
            culled += child.renderable ? 0 : 1
        })
        return culled
    }

    moveWorld(x: number, y: number) {
        if (this.offset.x === x && this.offset.y === y) {
            return
        }
        const loc = document.querySelector('.loc')
        const pcX = this.system.world._view[2]
        const pcY = this.system.world._view[3]
        if (loc !== null) {
            loc.innerHTML = `${Number(pcX).toFixed(0)}, ${Number(pcY).toFixed(0)} `
        }
        this.offset.x = x
        this.offset.y = y + 32
        this.assetLayers.forEach(layer => {
            layer.position.set(x, y + (32))
        })
    }

    tickPools() {
        // this.hits.tick()
    }


    reconcile(): void {
        const world = this.system.world
        // ////////////////////////////////////////////////////////////////////////////////////
        // PC DISCONNECT //////////////////////////////////////////////////////////////////////
        // ////////////////////////////////////////////////////////////////////////////////////
        world.disconnectedPCs.forEach(id => {
            const instancedIndex = this.system.renderManager.instancedPrefabs.findIndex(o => o.key === String(id))

            if (instancedIndex !== -1) {
                //@ts-ignore TODO - make an instanced prefab class that wraps the Prefab class
                if (this.instancedPrefabs[instancedIndex].value.destroy !== null) {
                    //@ts-ignore
                    this.instancedPrefabs[instancedIndex].value.destroy()
                }
                this.system.renderManager.instancedPrefabs = this.system.renderManager.instancedPrefabs.splice(instancedIndex, 1)
            }
            // TODO remove the child from the correct layer
            const pixiEntity = this.assetLayers.get('entities')?.getChildByName(String(id))
            if (pixiEntity) {
                pixiEntity.destroy()
            }
        })
        world.disconnectedPCs.length = 0

        // ////////////////////////////////////////////////////////////////////////////////////
        // ADD THINGS TO THE WORLD OR UPDATE THINGS ///////////////////////////////////////////
        // ////////////////////////////////////////////////////////////////////////////////////
        let layer = this.assetLayers.get('entities')
        const len = world._updatedEntitiesManager.count()

        for (let i = 0; i < len; i++) {
            const entityIndex = world._updatedEntitiesManager.getUpdatedEntity(i)
            // use a basic prefab to figure out what this entity is 
            // by reading the class and id
            world.loadEntityBytesIntoPrefab(entityIndex, this.system.renderManager.basePrefab)

            const basicProperties = this.system.renderManager.basePrefab.attributes
            const _class = String(basicProperties.find(o => o.key === 'class')?.value)
            const id = String(basicProperties.find(o => o.key === 'id')?.value)

            let prefab = this.system.renderManager.prefabManager.getPrefab(_class)

            if (prefab === undefined) {
                continue
            }

            if (prefab.staticOrInstanced === 'instanced') {
                const alreadyFound = this.system.renderManager.instancedPrefabs.find(o => o.key === id)
                if (alreadyFound) {
                    prefab = alreadyFound.value
                } else {
                    // @ts-ignore
                    prefab = new prefab.__proto__.constructor()
                    this.system.renderManager.instancedPrefabs.push({ key: id, value: prefab })
                    console.log(this.system.renderManager.instancedPrefabs)
                }
            }

            // check to see if a PIXI.Container has been created with this id
            const child: PIXI.Container = this.system.renderManager.objectReferences.get(id) as PIXI.Container
            const added = (child !== null) && (child !== undefined)

            if (added) {
                if (!prefab) { throw PrefabException('No Prefab') }
                world.loadEntityBytesIntoPrefab(entityIndex, prefab)
                prefab.update(0, child)
            } else {
                const p = prefab.create()
                p.name = id
                this.system.renderManager.objectReferences.set(id, p)
                world.loadEntityBytesIntoPrefab(entityIndex, prefab)
                prefab.update(0, p)
                layer?.addChild(p)
                world._updatedEntitiesManager.skip(entityIndex)
            }
        }
        world._updatedEntitiesManager.clearAndRestoreSkipped()
    }

    sort(): void {
        this.assetLayers.get('entities')?.children.sort((a, b) => a.position.y - b.position.y)
    }    
}


function PrefabException(message: string) {
    console.log(message)
}
