import * as PIXI from 'pixi.js'
import { Transformable } from '.'
import { UPSCALE_FACTOR } from '../../constants'

import animatedTemplate from '../templates/animated'

const nftId = window.localStorage.getItem('nft_id') || '0023'

export class Generic2DirectionalSprite extends Transformable {
  sizeX: number = -1
  sizeY: number = -1
  frameLengths: number[] = []
  animationLUT: number[] = []

  uniforms = {
    sizeMulti: 1,
    flowDistance: 0.5,
    flowX: 10.0,
    flowY: 10.0,
    stretchX: 40.0,
    stretchY: 40.0,
    time: Math.random() * 10240.0,
    size: 800,
    theta: Math.PI * 0.5,
    offset: Math.random(),
    timeX: 0.0,
    timeY: 0.0,
    centerPositionOffset: { x: 0.5, y: 0.5 },
    centerPositionOffsetSample: { x: 0.5, y: 0.5 },
    stacks: 1,
    mushroom1: PIXI.Texture.WHITE,
    mushroom2: PIXI.Texture.WHITE,
    mushroom3: PIXI.Texture.WHITE,
    mushroom4: PIXI.Texture.WHITE,
    mushroom5: PIXI.Texture.WHITE,
    mushroom6: PIXI.Texture.WHITE,
    mushroom7: PIXI.Texture.WHITE,
    mushroom8: PIXI.Texture.WHITE,
    mushroom9: PIXI.Texture.WHITE,
    mushroom10: PIXI.Texture.WHITE,
  }

  mushroomFilters: PIXI.Filter[] = []

  filters: PIXI.Filter[] | null = []

  tickerFn: any

  constructor() {
    super()
    this.template.load(animatedTemplate)
    this.unpackTemplate()
    this.staticOrInstanced = 'instanced'


    if(nftId !== null) {
      this.assets.push(['./output-sheets/',nftId,'_show - idle.png'].join(''))
      this.assets.push(['./output-sheets/',nftId,'_show - jog.png'].join(''))
    } else {
      this.assets.push('./idle.png')
      this.assets.push('./jog.png')
    }


    //@ts-ignore 
    window.uniforms = this.uniforms
  }

  create() {
    const updateUniformValues = (delta: number) => {
      // console.log(this.template.unpack().find(o=>o.key==='id')?.value)
      this.uniforms.time += delta
      this.uniforms.offset = ((Math.random() * 2) - 1) * 0.05
    }

    setTimeout(() => {
      this.uniforms.mushroom1 = PIXI.Texture.from(PIXI.Loader.shared.resources[`./images/mushrooms/mushroom_01.png`].data)
      this.uniforms.mushroom2 = PIXI.Texture.from(PIXI.Loader.shared.resources[`./images/mushrooms/mushroom_02.png`].data)
      this.uniforms.mushroom3 = PIXI.Texture.from(PIXI.Loader.shared.resources[`./images/mushrooms/mushroom_03.png`].data)
      this.uniforms.mushroom4 = PIXI.Texture.from(PIXI.Loader.shared.resources[`./images/mushrooms/mushroom_04.png`].data)
      this.uniforms.mushroom5 = PIXI.Texture.from(PIXI.Loader.shared.resources[`./images/mushrooms/mushroom_05.png`].data)
      this.uniforms.mushroom6 = PIXI.Texture.from(PIXI.Loader.shared.resources[`./images/mushrooms/mushroom_06.png`].data)
      this.uniforms.mushroom7 = PIXI.Texture.from(PIXI.Loader.shared.resources[`./images/mushrooms/mushroom_07.png`].data)
      this.uniforms.mushroom8 = PIXI.Texture.from(PIXI.Loader.shared.resources[`./images/mushrooms/mushroom_08.png`].data)
      this.uniforms.mushroom9 = PIXI.Texture.from(PIXI.Loader.shared.resources[`./images/mushrooms/mushroom_09.png`].data)
      this.uniforms.mushroom10 = PIXI.Texture.from(PIXI.Loader.shared.resources[`./images/mushrooms/mushroom_10.png`].data)
    }, 600)


    this.tickerFn = updateUniformValues
    PIXI.Ticker.shared.add(updateUniformValues)
    if (this.assets.length === 0) {
      throw new Error('Your prefab needs to implement an asset')
    } else {
      return this.convertSpriteSheet()
    }
  }

  update(delta: number, child: PIXI.Container) {
    super.update(delta, child)

    const thetaLUT = [
      -1,  //0
      0.5,  //1
      0.5,  //2
      0,  //3
      0.75,  //4
      0.25,  //5
      -0.75,  //6
      -.25  //7
    ]

    // left 1,5,4
    // right 2,6,7

    const flowLut = [
      { x: 0, y: 1 }, //0
      { x: -1, y: 1 },
      { x: 1, y: 1 }, //2
      { x: 0, y: -1 },
      { x: -1, y: 1 }, //4
      { x: -1, y: -1 },
      { x: 1, y: 1 }, //6
      { x: 1, y: -1 },
    ]

    const flowMulti = -20

    let direction = Number(this.attributes.find(o => o.key === 'direction')?.value)

    if (direction < 8) {
      this.uniforms.flowX = flowLut[direction % 8].x * flowMulti
      this.uniforms.flowY = flowLut[direction % 8].y * flowMulti
    } else {
      this.uniforms.flowX = 0
      this.uniforms.flowY = 0.75 * flowMulti
    }

    this.uniforms.theta = thetaLUT[direction % 8] * Math.PI

    child.children.forEach(c => { c.renderable = false })
    child.children[direction].renderable = true

  }

  convertSpriteSheet() {
    // add filters and enable them based on which mushroom is being used
    // increase the parameters if multiple of the same mushroom
    const _container = new PIXI.Container()

    const characterShaders = [
      'demo_shader_0',    // 0
      'demo_shader_1',    // 1
      'demo_shader_2',    // 2
      'demo_shader_3',    // 3
      'rainbow_dog_frag', // 4
      'demo_shader_4',    // 5
      'demo_shader_5',    // 6
      'demo_shader_6',    // 7
      'demo_shader_7',    // 8
      'demo_shader_8'     // 9
    ]

    characterShaders.forEach(shader_name => {
      const filter = new PIXI.Filter(undefined, PIXI.Loader.shared.resources[shader_name].data, this.uniforms)
      filter.autoFit = false
      filter.enabled = true
      filter.padding = 0
      filter.resolution = 1
      this.mushroomFilters.push(filter)
    })

    _container.filters = [
      // this.mushroomFilters[3],
      // this.mushroomFilters[1],
      // this.mushroomFilters[1],
    ]
    this.filters = _container.filters

    const tint = 0xFFFFFF
    const scale = 1.0

    const size = nftId === null ? 64.0 : 96.0
    const nFrames = [9, 8]
    const sheetWidth = [9, 8]

      ;[1, 0].forEach(n => {
        const textureAtlas = PIXI.Texture.from(this.assets[n])
        const baseTexture = textureAtlas.castToBaseTexture()

        nFrames[n] = textureAtlas.width / size
        sheetWidth[n] = nFrames[n]

        const textures: PIXI.Texture[] = []
        for (let i = 0; i < nFrames[n] * 8; i++) {
          const y = Math.floor(i / sheetWidth[n])
          const x = i - (y * sheetWidth[n])
          textures.push(new PIXI.Texture(baseTexture, new PIXI.Rectangle(x * size, y * size, size, size)))
        }

        for (let i = 0; i < 8; i++) {
          const sprite = new PIXI.AnimatedSprite(textures.slice(i * nFrames[n], (i * nFrames[n]) + nFrames[n]))
          sprite.animationSpeed = 1.5 / nFrames[n]
          sprite.gotoAndPlay(0)
          sprite.anchor.set(0.5, 0.85)
          sprite.scale.set(scale)

          sprite.tint = tint
          sprite.renderable = true

          _container.addChild(sprite)
        }
      })

    let pow2Width = Math.pow(2, Math.ceil(Math.log(Math.ceil(window.innerWidth / UPSCALE_FACTOR)) / Math.log(2)))
    let pow2Height = Math.pow(2, Math.ceil(Math.log(Math.ceil(window.innerHeight / UPSCALE_FACTOR)) / Math.log(2)))

    const sizeX = 128
    const sizeY = 128

    _container.filterArea = new PIXI.Rectangle(
      (pow2Width * 0.5) - sizeX,
      (pow2Height * 0.5) - sizeY,
      sizeX * 2,
      sizeY * 2)

    return _container
  }
}