import * as PIXI from 'pixi.js'
import { GenericAnimatedSprite } from '../extendable'
import killableTemplate from '../templates/killable'

export class IceElemental extends GenericAnimatedSprite {
    constructor() {
        super()
        this.template.load(killableTemplate)
        this.unpackTemplate()
        this.assets.push('./images/ice-elemental.png')
        this.sizeX = 32
        this.sizeY = 32
        this.frameLengths = [6, 4, 8, 7, 5, 3, 7]
    }
}