export default class MetricsManager {
    panels: StatsPanel[]

    constructor() {
        this.panels = [
            new StatsPanel({ name: 'Render (ms)', min: 0, max: 16.7 * 2, mode: 'interframe' }),
            new StatsPanel({ name: 'World Speed (ms)', min: 0, max: .5 }),
            new StatsPanel({ name: 'World Updates (n)', min: 0, max: 300 }),
            new StatsPanel({ name: 'World Skips (n)', min: 0, max: 300 }),
            new StatsPanel({ name: 'Network Down', min: 0, max: 1000 }),
            new StatsPanel({ name: 'Network Up', min: 0, max: 1000 }),
            new StatsPanel({ name: 'Ping (ms)', min: 0, max: 2000 })
        ]
    }

    updatePanels(array: number[]) {
        for (let i = 0; i < array.length; i++) {
            this.panels[i].update(array[i])
        }
    }

    addPanel(panel: StatsPanel) {
        this.panels.push(panel)
    }
}

export class StatsPanel {
    avg: number
    name: string
    min: number
    max: number
    mode: string // TODO make enum
    setMin: number
    setMax: number
    ctx: CanvasRenderingContext2D

    history: RingBuffer

    dimensions = { x: 256, y: 32 }
    runningValue: number = 0

    constructor(options: any) {
        this.avg = 0
        this.name = options.name
        this.min = Math.pow(2, 16)
        this.max = options.max | 0
        this.setMin = 0
        this.setMax = this.max
        this.history = new RingBuffer(this.dimensions.x)
        this.mode = options.mode ? String(options.mode) : 'count'

        const canvas = document.createElement('canvas')


        canvas.addEventListener('click', () => {
            this.min = Math.pow(2, 16)
            this.max = 0
        })

        canvas.addEventListener('wheel', event => { event.preventDefault() })
        canvas.addEventListener('mousewheel', event => { event.preventDefault() })


        this.ctx = <CanvasRenderingContext2D>canvas.getContext('2d')
        this.ctx.font = '32px Monospace,Helvetica,Arial,sans-serif';

        canvas.width = this.dimensions.x
        canvas.height = this.dimensions.y * 2
        document.querySelector('.stats')?.appendChild(canvas)
    }

    update(value: number) {
        if (this.mode === 'interframe') {
            this.updateInterframe(value)
        } else {
            this.updateCount(value)
        }
    }

    updateCount(value: number) {
        value = Math.max(value, 0)
        if (this.min > value) {
            this.min = value
        }
        if (this.max < value) {
            this.max = value
        }
        this.history.update(value)
        this.avg = average(this.history.buffer)
        this.draw()
    }

    updateInterframe(value: number) {
        this.updateCount(value - this.runningValue)
        this.runningValue = value
    }

    draw() {
        // console.log(this.min)
        const v = (this.history.latest() / (this.setMax - this.setMin))
        this.ctx.strokeStyle = 'none'

        // bg
        this.ctx.fillStyle = '#000'
        this.ctx.fillRect(this.history.index, 0, 1, 32)

        // line
        this.ctx.fillStyle = '#3366FF'
        this.ctx.fillRect((this.history.index + 1) % this.history.size, 0, 1, 32)

        // value
        this.ctx.fillStyle = this.history.latest() > this.avg ? '#F33' : '#AAA'
        this.ctx.fillRect(this.history.index, 32 - (v * 32), 1, (v * 32))

        // label
        this.ctx.fillStyle = '#000'
        this.ctx.fillRect(0, this.dimensions.y, this.dimensions.x, this.dimensions.y)

        this.ctx.fillStyle = '#FFF'
        this.ctx.font = 'bold 11px Helvetica,Arial'
        this.ctx.fillText(`\
${this.name} - \
${[
                round(this.avg),
                round(this.history.latest()),
                round(this.min),
                round(this.max),
            ].join(' ')}\
`, 6, 32 + 22);
    }
}

function round(v: number): string {
    return String(v.toFixed(1))
}

function average(buffer: Float32Array | Uint32Array): number {
    let sum = 0
    for (let i = 0; i < buffer.length; i++) {
        sum += buffer[i]
    }
    return sum / buffer.length
}

export class RingBuffer {
    buffer: Uint32Array | Float32Array
    index: number
    memory: ArrayBuffer
    size: number

    constructor(length: number) {
        this.index = 0
        this.size = length
        this.memory = new ArrayBuffer(length * 4)
        this.buffer = new Float32Array(this.memory)
    }

    latest(): number {
        if (this.index !== 0) {
            return this.buffer[this.index - 1]
        } else {
            return this.buffer[this.size - 1]
        }
    }

    update(value: number) {
        this.buffer[this.index] = value
        this.index += 1
        this.index = this.index % this.buffer.length
    }
}