import { ENTITY_LENGTH, MAX_ENTITIES, SIZEOF_INT16 } from '../../constants'

export default class UpdatedEntitiesManger {
    private _buffer: ArrayBuffer;
    private _view: Uint16Array;

    private _skippedBuffer: ArrayBuffer;
    _skippedView: Uint16Array;

    constructor() {
        this._buffer = new ArrayBuffer((ENTITY_LENGTH * MAX_ENTITIES * SIZEOF_INT16) + SIZEOF_INT16)
        this._view = new Uint16Array(this._buffer)
        this._skippedBuffer = new ArrayBuffer((ENTITY_LENGTH * MAX_ENTITIES * SIZEOF_INT16) + SIZEOF_INT16)
        this._skippedView = new Uint16Array(this._skippedBuffer)

        this._view[0] = 0
        this._skippedView[0] = 0
    }

    add(entityIndex: number) {
        let currentIdx: number = this.count()
        this._view[currentIdx + 1] = entityIndex
        this.incrementCount()
    }

    clearAndRestoreSkipped() {
        this._view[0] = 0
        const skipCount = this._skippedView[0]
        if (skipCount > 0) {
            for (let i = 0; i < skipCount + 1; i++) {
                this._view[i] = this._skippedView[i]
            }
            this._skippedView[0] = 0
        }
    }

    count(): number {
        return this._view[0]
    }

    countSkipped(): number {
        return this._skippedView[0]
    }

    getUpdatedEntity(i: number): number {
        return this._view[i + 1]
    }

    incrementCount() {
        this._view[0] += 1
    }

    skip(entityIndex: number) {
        let currentIdx: number = this._skippedView[0]
        this._skippedView[currentIdx + 1] = entityIndex
        this._skippedView[0] += 1
    }
}
