import * as PIXI from 'pixi.js'
import prefabs_definitions from '../../definitions/prefabs'
import PrefabManager from '../Prefab/PrefabManager'

//@ts-ignore
import portalFragmentShader from '../../shaders/polar-coordinates-noise-portal.glsl'

console.log()

export default class AssetManager {
    prefabManager: PrefabManager
    assets: string[] = []
    constructor() {
        this.prefabManager = new PrefabManager()
        prefabs_definitions.forEach(definition => {
            this.prefabManager.getPrefab(definition.key).assets.forEach(asset => {
                if (this.assets.indexOf(asset) === -1) {
                    this.assets.push(asset)
                }
            })
        })

        // console.log('assets found in the prefabs')
        // console.log(this.assets.join('\n'))
    }

    loadAssets(cb: Function) {
        // ['lights_frag', '/shaders/lights-easy.frag'],

        // TODO, priority low - figure out how to remove this section
        const nonPrefabAssets = [
            ['tiled_level', './images/Tiled_village/test_village.tmx'],
            ['compositor_frag', '/shaders/compositor.frag'],
            ['default_frag', '/shaders/default.frag'],
            ['fire_and_smoke_frag', '/shaders/fire-and-smoke.frag'],
            ['fire_and_smoke_dog_frag', '/shaders/fire-and-smoke-dog.frag'],
            ['fog_frag', '/shaders/fog.frag'],
            ['ground_shadow_frag', '/shaders/ground-shadow.frag'],
            ['easy_lights_frag', '/shaders/lights-easy.frag'],
            ['god_rays_frag', '/shaders/god-rays.frag'],
            ['old_tv_frag', '/shaders/old-tv.frag'],
            ['rainbow_bridge_frag', '/shaders/rainbow-bridge.frag'],
            ['rainbow_dog_frag', '/shaders/rainbow-dog.frag'],
            ['rgb_glitch_frag', '/shaders/rgb-glitch.frag'],
            ['solid_color_frag', '/shaders/solid-color.frag'],
            ['super_fire_and_smoke_frag', require('../../shaders/super-fire-and-smoke.glsl')],
            ['portal_frag', require('../../shaders/polar-coordinates-noise-portal.glsl')],
            ['black_hole_frag', require('../../shaders/black-hole-bend-light.glsl')],
            ['water_frag', require('../../shaders/water.glsl')],
            ['procedural_ground_frag', require('../../shaders/aseprite-shaders/procedural-ground-shift.glsl')],
            ['procedural_ground_edge_frag', require('../../shaders/aseprite-shaders/procedural-ground-edge.glsl')],
            ['procedural_ground_color_frag', require('../../shaders/aseprite-shaders/procedural-ground-color.glsl')],
            ['lights_frag', require('../../shaders/lights.glsl')],
            ['lights_compute_x_frag', require('../../shaders/lights-calculate-x.glsl')],
            ['lights_compute_y_frag', require('../../shaders/lights-calculate-y.glsl')],
            ['lights_compute_reducer_frag', require('../../shaders/lights-calculate-reducer.glsl')],
            ['webgl2_vert', require('../../shaders/webgl2/vert.glsl')],
            ['webgl2_frag', require('../../shaders/webgl2/frag.glsl')],
            ['grass_move_frag', require('../../shaders/grass-move.glsl')],
            ['demo_shader_0', require('../../shaders/demo_shader_0.glsl')],
            ['demo_shader_1', require('../../shaders/demo_shader_1.glsl')],
            ['demo_shader_2', require('../../shaders/demo_shader_2.glsl')],
            ['demo_shader_3', require('../../shaders/demo_shader_00.glsl')],// we do not use demo_shader_3
            ['demo_shader_4', require('../../shaders/demo_shader_4.glsl')],
            ['demo_shader_5', require('../../shaders/demo_shader_5.glsl')],
            ['demo_shader_6', require('../../shaders/demo_shader_6.glsl')],
            ['demo_shader_7', require('../../shaders/demo_shader_7.glsl')],
            ['demo_shader_8', require('../../shaders/demo_shader_8.glsl')],
            ['pixel_animation', '/sprite-sheets/idle_animation_sheet.json'],
            ['village-dirt', './images/new-test-village/ntv-dirt.png'],
            // ['village-collision', './images/new-test-village/ntv-collision.png'],
            ['village-grass', './images/new-test-village/ntv-grass.png'],
            ['village-dock', './images/new-test-village/ntv-dock.png'],
            ['village-water', './images/new-test-village/ntv-water.png'],
            // ['village-mask-grass', './images/new-test-village/ntv-grass-mask.png'],
            // ['village-mask-grass-buildings', './images/new-test-village/ntv-grass-mask-buildings.png'],
            ['village-mask-water', './images/new-test-village/ntv-water.png'],
            ['dock-assets', './images/Tiled_village/tiles/dock/dock_tileset.png']
        ].forEach(asset => {
            if (asset.length === 2) {
                PIXI.Loader.shared.add(asset[0], asset[1])
            } else if (asset.length === 1) {
                PIXI.Loader.shared.add(asset[0])
            }
        })

        this.assets.forEach(asset => {
            PIXI.Loader.shared.add(asset)
        })

        PIXI.Loader.shared.load(() => {
            // console.log(PIXI.Loader.shared.resources)
            cb()
        })

    }

}