import * as PIXI from 'pixi.js'
import { Transformable } from '.'

import animatedTemplate from '../templates/animated'

export class Generic8DirectionalSprite extends Transformable {
    sizeX: number = -1
    sizeY: number = -1
    frameLengths: number[] = []
    animationLUT: number[] = []

    uniforms = {
        sizeMulti: 1,
        flowDistance: 0.5,
        flowX: 10.0,
        flowY: 10.0,
        stretchX: 40.0,
        stretchY: 40.0,
        time: Math.random() * 10240.0,
        size: 800,
        theta: Math.PI * 0.5,
        offset: Math.random(),
        timeX: 0.0,
        timeY: 0.0
    }

    mushroomFilters: PIXI.Filter[] = []

    filters: PIXI.Filter[] | null = []

    buddyOn = false

    tickerFn: any

    constructor() {
        super()
        this.template.load(animatedTemplate)
        this.unpackTemplate()
        this.staticOrInstanced = 'instanced'
        this.assets.push('./images/wolf_no-edge_full.png')
        this.toggleBuddy = this.toggleBuddy.bind(this)

        //@ts-ignore
        window.toggleBuddy = this.toggleBuddy

    }

    toggleBuddy () {
        this.buddyOn = !this.buddyOn
    }

    create() {
        const updateUniformValues = (delta: number) => {
            // console.log(this.template.unpack().find(o=>o.key==='id')?.value)
            this.uniforms.time += delta
            this.uniforms.offset = ((Math.random() * 2) - 1) * 0.05
        }
        this.tickerFn = updateUniformValues
        PIXI.Ticker.shared.add(updateUniformValues)
        if (this.assets.length === 0) {
            throw new Error('Your prefab needs to implement an asset')
        } else {
            return this.convertSpriteSheet()
        }
    }

    update(delta: number, child: PIXI.Container) {
        super.update(delta, child)
        if (this.buddyOn) {
            const thetaLUT = [
                -1,  //0
                0.5,  //1
                0.5,  //2
                0,  //3
                0.75,  //4
                0.25,  //5
                -0.75,  //6
                -.25  //7
            ]

            const flowLut = [
                { x: 0, y: 1 }, //0
                { x: -1, y: 1 },
                { x: 1, y: 1 }, //2
                { x: 0, y: -1 },
                { x: -1, y: 1 }, //4
                { x: -1, y: -1 },
                { x: 1, y: 1 }, //6
                { x: 1, y: -1 },
            ]

            const flowMulti = -20

            const direction = Number(this.attributes.find(o => o.key === 'direction')?.value)

            if (direction < 8) {
                this.uniforms.flowX = flowLut[direction % 8].x * flowMulti
                this.uniforms.flowY = flowLut[direction % 8].y * flowMulti
            } else {
                this.uniforms.flowX = 0
                this.uniforms.flowY = 0.75 * flowMulti
            }

            this.uniforms.theta = thetaLUT[direction % 8] * Math.PI

            child.children.forEach(c => { c.renderable = false })
            child.children[direction].renderable = true
        } else {
            child.children.forEach(c => { c.renderable = false })
        }
    }

    convertSpriteSheet() {
        // add filters and enable them based on which mushroom is being used
        // increase the parameters if multiple of the same mushroom

        const textureAtlas = PIXI.Texture.from(this.assets[0])
        const baseTexture = textureAtlas.castToBaseTexture()

        const _container = new PIXI.Container()

        const characterShaders = [
            'demo_shader_0',
            'demo_shader_1',
            'demo_shader_2',
            'demo_shader_3',
            'rainbow_dog_frag',
            // 'fire_and_smoke_dog_frag'
        ]

        characterShaders.forEach(shader_name => {
            const filter = new PIXI.Filter(undefined, PIXI.Loader.shared.resources[shader_name].data, this.uniforms)
            filter.autoFit = false
            filter.enabled = true
            filter.padding = 16
            filter.resolution = 1
            this.mushroomFilters.push(filter)
        })

        _container.filters = [
            // this.mushroomFilters[3],
            // this.mushroomFilters[1],
            // this.mushroomFilters[1],
        ]
        this.filters = _container.filters

        const tint = 0xAAAAAA
        // const tint = 0x333333
        const scale = 1.0

        this.frameLengths.forEach((frameLength, frameIndex) => {
            const textures: PIXI.Texture[] = []
            for (let i = 0; i < frameLength; i++) {
                textures.push(
                    new PIXI.Texture(
                        baseTexture,
                        new PIXI.Rectangle(
                            Math.floor(i * this.sizeX) + 0.0,
                            Math.floor(frameIndex * this.sizeY) + 0.0,
                            this.sizeX,
                            this.sizeY
                        )
                    )
                )
            }
            const sprite = new PIXI.AnimatedSprite(textures)
            // console.log(sprite)
            sprite.name = `animation_set_${frameIndex}`
            sprite.animationSpeed = 0.15 //1.0 / frameLength
            sprite.gotoAndPlay(Math.floor(Math.random() * frameLength))
            sprite.anchor.set(0.5, 1.0)
            sprite.scale.set(scale)

            sprite.tint = tint
            sprite.renderable = false
            _container.addChild(sprite)
        })

        // stopped sprites
        this.frameLengths.forEach((frameLength, frameIndex) => {
            const textures: PIXI.Texture[] = []
            for (let i = 7; i < 8; i++) {
                textures.push(
                    new PIXI.Texture(
                        baseTexture,
                        new PIXI.Rectangle(
                            Math.floor(i * this.sizeX) + 0.0,
                            Math.floor(frameIndex * this.sizeY) + 0.0,
                            this.sizeX,
                            this.sizeY
                        )
                    )
                )
            }
            const sprite = new PIXI.AnimatedSprite(textures)
            // console.log(sprite)
            sprite.name = `animation_set_${frameIndex}`
            sprite.tint = tint
            sprite.animationSpeed = 0.15 //1.0 / frameLength
            sprite.gotoAndPlay(Math.floor(Math.random() * 1))
            sprite.anchor.set(0.5, 1.0)
            sprite.scale.set(scale)
            sprite.renderable = false
            _container.addChild(sprite)
        })

        // _container.children[2].renderable = true

        return _container
    }
}