//@ts-nocheck - this is turned off whiel we develop in single player mode for a while
// import { Manager, Socket } from "socket.io-client";
import EventManager from '../Core/EventManager'

import type World from '../World/World'
import type System from '../Core/System'

export default class NetworkManager {
    manager: Manager
    networkIn: number = 0
    networkOut: number = 0
    ping: number = 0
    socket: Socket
    world: World

    constructor(system: System) {
        this.world = system.world

        return

        this.manager = new Manager();
        this.socket = this.manager.socket("/")

        this.manager.on('close', () => {
            setTimeout(() => { window.location.reload() }, 200)
        })

        const latency = 0
        const emitFn = this.socket.emit
        //@ts-ignore
        this.socket.emit = (...args) => setTimeout(() => {
            emitFn.apply(this.socket, args)
        }, latency)

        hackyAuthentication(this.socket)

        setInterval(() => {
            this.socket.emit('ping', { time: Date.now() })
        }, 250)

        this.socket.on('hits', msg => {
            EventManager.emit('hits', msg)
        })

        this.socket.on('pong', msg => {
            // console.log(Date.now() - msg.time)
            this.ping = Date.now() - msg.time
        })

        this.socket.on('identity', d => {
            // console.log('got identity')
            this.world.setIdentity({ id: Number(d.id), address: Number(d.address) })
        })

        this.socket.on('pc-disconnected', msg => {
            this.world.disconnectedPCs.push(parseInt(msg.id))
        })

        this.socket.on('world_update', d => {
            this.networkIn += d.length
            let tooMany = 0
            let i = 0;
            let address: number, len: number, msg: number[]
            while (i < d.length - 1) {
                address = d[i]
                len = d[i + 1]
                msg = d.slice(i + 2, i + 2 + len)
                i += len + 2
                this.world.write(address, len, msg)
                tooMany++
                if (tooMany === d.length) {
                    i = Number.MAX_SAFE_INTEGER
                    console.log('breaking')
                    break;
                }
            }
        })

    }

    getBytesDown(): number { return this.networkIn }
    getBytesUp(): number { return this.networkOut }
    getPing(): number { return this.ping }

    clearStats() {
        this.networkIn = 0
        this.networkOut = 0
    }

}

function hackyAuthentication(socket: Socket) {
    const token = window.localStorage.getItem('perfectly-fungible-token')
    if (token !== null) {
        socket.emit('authenticate', token)
    }
}