import * as PIXI from 'pixi.js'
import { Transformable } from '../extendable'

export class SuperTorch extends Transformable {
    uniforms = {
        sizeMulti: 1,
        flowDistance: 0.5,
        flowX: 0.0,
        flowY: 10 + (10 * Math.random()),
        stretchX: 40.0,
        stretchY: 40.0,
        time: Math.random() * 10240.0,
        size: 800
    }

    heatUniforms = {
        time: 0.0,
        uBg: {},
        renderTextureDimensions: { x: 0, y: 0 }
    }

    startTime: number = 0
    speed: number = 0
    tickerFn: any

    constructor() {
        super()
        this.assets.push('./images/fire-base.png')
        this.assets.push('./images/fire-flame-bigger.png')
        this.staticOrInstanced = 'instanced'
        this.startTime = Date.now()
        this.speed = Math.random() * 0.1
    }

    create() {
        const updateUniformValues = (delta: number) => {
            // console.log(this.template.unpack().find(o=>o.key==='id')?.value)
            this.uniforms.time += delta
            this.heatUniforms.time += delta
        }
        this.tickerFn = updateUniformValues
        PIXI.Ticker.shared.add(updateUniformValues)
        return this.convertSlime()
    }

    destroy() {
        PIXI.Ticker.shared.remove(this.tickerFn)
    }

    update(delta: number, child: PIXI.Container) {
        super.update(delta, child)
    }

    convertSlime() {

        const heatEffectFilter = new PIXI.Filter(undefined, PIXI.Loader.shared.resources.super_fire_and_smoke_frag.data, this.heatUniforms)
        heatEffectFilter.autoFit = false
        const rgbGlitchFilter = new PIXI.Filter(undefined, PIXI.Loader.shared.resources.fire_and_smoke_frag.data, this.uniforms)

        const _container = new PIXI.Container()

        const heatEffectSprite = new PIXI.Sprite(PIXI.RenderTexture.create({ width: 32, height: 64 }))
        // heatEffectSprite.filterArea = new PIXI.Rectangle(-32,-32,64,64)
        heatEffectSprite.filters = [heatEffectFilter]
        const baseSprite = new PIXI.Sprite(PIXI.Texture.from(PIXI.Loader.shared.resources[this.assets[0]].data))
        const flameSprite = new PIXI.Sprite(PIXI.Texture.from(PIXI.Loader.shared.resources[this.assets[1]].data))

        heatEffectSprite.anchor.set(0.5, 1.2)
        baseSprite.anchor.set(0.5, 1)
        flameSprite.anchor.set(0.5, 1)

        rgbGlitchFilter.autoFit = false
        flameSprite.filters = [rgbGlitchFilter]

        _container.addChild(heatEffectSprite)
        _container.addChild(baseSprite)
        _container.addChild(flameSprite)

        return _container
    }
}