import { useEffect, useState } from "react";

import { UICommandMap } from "../../constants";
import { UIScreen } from "../../enums";
import { useEventListener } from "../../hooks";
import { UICommand } from "../../types";

import ControlsView from "./ControlsView";
import DisclaimerView from "./DisclaimerView";
import FAQView from "./FAQView";

import StatsView from "./StatsView";

function UIView() {
    const [activeUIView, setActiveUI] = useState<UIScreen>(UIScreen.NONE);
    const [startedGame, setStartedGame] = useState<boolean>(false);

    const handleKeyPress = (e: any) => {
        if (startedGame === false) {
            setActiveUI(UIScreen.NONE);
            setStartedGame(true);
            return;
        }
        const command: UICommand = e.key.toLowerCase();
        if (Object.keys(UICommandMap).includes(command)) {
            const viewToActivate = UICommandMap[command];
            const deactivateAll = viewToActivate === activeUIView;
            setActiveUI(deactivateAll ? UIScreen.NONE : viewToActivate);
        }
    }

    useEventListener('keypress', handleKeyPress)

    useEffect(() => {
        setActiveUI(UIScreen.Disclaimer)
    }, [])

    return (
        <>
            {activeUIView === UIScreen.Controls && <ControlsView />}
            {activeUIView === UIScreen.Disclaimer && <DisclaimerView />}
            {activeUIView === UIScreen.FAQ && <FAQView />}
        </>
    )
}

export default UIView;
