import { useEffect, useState } from 'react'
import ProtectedRoute from './components/ProtectedRoute';

import Game from './routes/Game';
import Auth from './routes/Auth';

import './App.css'

import EventManager from "./engine/lib/Core/EventManager"
import SelectNFT from './SelectNFT'

import { UPSCALE_FACTOR } from './engine/constants/configurable'
import StatsView from './views/UIView/StatsView';
const w = window.innerWidth
const h = window.innerHeight

function App() {

    const [worldPosition,setWorldPosition] = useState<number[]>([0,0])

    useEffect(() => {
        EventManager.on('position-update', e => {
            // console.log('position', e)
            setWorldPosition(e.value)
        })
    }, [])

    return <div id="interface" className="fullscreen">
        <StatsView/>
        <Game />
        <SelectNFT />
    </div>
}

export default App;
