import * as PIXI from 'pixi.js'
import { Transformable } from '../extendable'

export class QuestGiver extends Transformable {
    constructor() {
        super()
        this.assets.push('./images/placeholder-text.png')
        this.assets.push('./images/quest-indicator.png')
    }

    create() {
        const sprite = new PIXI.Sprite(PIXI.Texture.from(
            PIXI.Loader.shared.resources[this.assets[0]].data
        ))
        sprite.name = JSON.stringify({ layer: 'overlay', type: 'placeholder-text' })
        sprite.position.set(-10, -50)
        sprite.anchor.set(1, 1)

        const sprite2 = new PIXI.Sprite(PIXI.Texture.from(
            PIXI.Loader.shared.resources[this.assets[1]].data
        ))
        sprite2.name = JSON.stringify({ layer: 'entities', type: 'quest-indicator' })
        sprite2.position.set(0, -55)
        sprite2.anchor.set(0.5, 1)

        const container = new PIXI.Container()
        container.name = 'pbr'

        container.addChild(sprite)
        container.addChild(sprite2)
        return container
    }

    update(delta: number, child: PIXI.Container) {
        super.update(delta, child)
    }
}