    tickLevel() {

        const world = this.system.world
        // ////////////////////////////////////////////////////////////////////////////////////
        // MOVE WORLD BASED ON PC POSITION ////////////////////////////////////////////////////
        // ////////////////////////////////////////////////////////////////////////////////////
        // get world entity position
        // todo, this is hardcoded, unpack it from the prefab to get the right bytes
        if (world.identity.address !== -1) {
            const movement = { x: 0, y: 0 }
            if (this.system.interactionManager.keyboardManager.pressedKeys.indexOf('KeyA') !== -1) {
                movement.x -= 1
            }
            if (this.system.interactionManager.keyboardManager.pressedKeys.indexOf('KeyD') !== -1) {
                movement.x += 1
            }
            if (this.system.interactionManager.keyboardManager.pressedKeys.indexOf('KeyW') !== -1) {
                movement.y -= 1
            }
            if (this.system.interactionManager.keyboardManager.pressedKeys.indexOf('KeyS') !== -1) {
                movement.y += 1
            }

            const hypotMult = (Math.SQRT2 / 2.0) * 1.1
            if (Math.abs(movement.y) === 1.0 && Math.abs(movement.x) === 1.0) {
                movement.x *= hypotMult
                movement.y *= hypotMult
            }

            if (this.system.interactionManager.keyboardManager.pressedKeys.indexOf('ShiftLeft') !== -1) {
                movement.x *= 4
                movement.y *= 4
            } else {
                movement.x *= 3
                movement.y *= 3
            }

            // movement.y *= 0.8

            const newX = Math.round(world._view[world.identity.address + 2] + movement.x)
            const newY = Math.round(world._view[world.identity.address + 3] + movement.y)
            let animation = world._view[world.identity.address + 4]

            if (movement.x < 0 && movement.y < 0) {             // NW
                animation = 3
            } else if (movement.x < 0 && movement.y == 0) {     // W
                animation = 4
            } else if (movement.x < 0 && movement.y > 0) {      // SW
                animation = 5
            } else if (movement.x == 0 && movement.y < 0) {     // N
                animation = 2
            } else if (movement.x == 0 && movement.y > 0) {     // S
                animation = 6
            } else if (movement.x > 0 && movement.y < 0) {     // NE
                animation = 1
            } else if (movement.x > 0 && movement.y == 0) {     // E
                animation = 0
            } else if (movement.x > 0 && movement.y > 0) {     // SE
                animation = 7
            } else if (movement.x === 0 && movement.y === 0) {
                if (animation < 8) {
                    animation += 8  // do the corresponding stop animation 8 animations later
                }
            }

            let doCollision = false
            let testpt = -1
            let testptWater = -1
            if (this.collisionData.length > 0) {
                testpt = this.collisionData[
                    (4 * ((newY * this.collisionCanvas.width) + newX)) + 3
                ]
            }
            if (this.waterData.length > 0) {
                testptWater = this.waterData[
                    (4 * ((newY * this.waterCanvas.width) + newX)) + 3
                ]
            }

            if (!doCollision || testpt !== 255) {
                // update the player
                world.write(2, 2, [newX, newY])
                world._view[world.identity.address + 4] = animation

                if (movement.x !== 0 || movement.y !== 0) {
                    this.dogBuffer.push([newX, newY, animation])
                } else {

                }

                const dogX = world._view[world.identity.address + 2 + 8]
                const dogY = world._view[world.identity.address + 3 + 8]
                // const d = Math.sqrt(Math.pow(newX - dogX, 2) + Math.pow(newY - dogY, 2))

                if (this.dogBuffer.length > 20) {
                    const dogStuff = this.dogBuffer.shift()
                    // update the dog
                    if (dogStuff !== undefined) {
                        world.write(10, 2, [dogStuff[0], dogStuff[1]])  // position
                        world._view[world.identity.address + 12] = dogStuff[2]  // animation
                    }
                } else {
                    world.write(12, 1, [animation])
                }
                const px = Math.floor((this.system.renderManager.textureManager.dimensions.x * 0.5) - world._view[world.identity.address + 2])
                const py = Math.floor((this.system.renderManager.textureManager.dimensions.y * 0.5) - world._view[world.identity.address + 3])
                this.system.renderManager.layerManager.moveWorld(px, py)
            }

            if (testptWater === 255) {
                this.system.demoManager.mushroomManager.wash()
            }
        }

        const playerX = this.system.world._view[2]
        const playerY = this.system.world._view[3]

        EventManager.emit('position-update', { value: [playerX, playerY] })

        this.npcs.forEach(npc => {
            if (npc.value.renderable === false) {
                return
            }
            let d = Math.sqrt(Math.pow(npc.value.position.x - playerX, 2) + Math.pow(npc.value.position.y - playerY, 2))
            if (d < 20) {
                npc.value.renderable = false
                const spriteIdx = JSON.parse(npc.value.name).spriteIdx
                this.mushroomManager.pickup(spriteIdx)
            }
        })

        this.interactionLocations.forEach(location => {
            const lastLocation = this.interactionHistory[this.interactionHistory.length - 1]
            if (Date.now() - lastLocation.time < 1000) {
                return
            }
            if (lastLocation.code === location.code) {
                return
            }
            let d = Math.sqrt(Math.pow(location.position.x - playerX, 2) + Math.pow(location.position.y - playerY, 2))
            if (d < location.distance) {
                this.interactionHistory.push({ code: location.code, time: Date.now() })
                console.log(lastLocation.code, location.code)
                EventManager.emit('world-interaction', { value: location.code })
            }
        })
    }
