import * as PIXI from 'pixi.js'
import { Transformable } from '../extendable'

export class Ground extends Transformable {
    constructor() {
        super()
        this.assets.push('./images/orthographic-ground.png')
    }

    create() {
        const sprite = new PIXI.Sprite(PIXI.Texture.from(PIXI.Loader.shared.resources[this.assets[0]].data))
        // sprite.tint = Math.random() * 0xFFFFFF
        sprite.tint = 0x777777
        // sprite.anchor.set(0.5,0.5)
        return sprite
    }

    update(delta: number, child: PIXI.Container) {
        super.update(delta, child)
    }
}