import * as PIXI from 'pixi.js'
import { Transformable } from '../extendable'

export class SimpleParticleSystem extends Transformable {
    age: number = 0
    constructor() {
        super()
        this.assets.push('/images/barrel.png')
    }

    create() {
        const p = new PIXI.Container()
        const particleContainer = new PIXI.Container()

        let _t = 0

        let m = Math.random()
        let _m = Math.random()

        // m = _m = 0.1

        let n = 10

        for (let i = 0; i < n; i++) {
            particleContainer.addChild(new PIXI.Sprite(PIXI.Texture.from(PIXI.Loader.shared.resources[this.assets[0]].data)))
        }
        p.addChild(particleContainer)
        PIXI.Ticker.shared.add(tick)
        // return this.particleContainer
        return p

        function tick(delta: number) {
            _t += delta
            for (let i = 0; i < n; i++) {
                particleContainer.children[i].position.x = window.innerWidth * 0.5 * Math.sin((_t * 0.01 * m) + i)
                particleContainer.children[i].position.y = window.innerHeight * 0.5 * Math.cos((_t * 0.01 * _m) + i)
            }
            // console.log(delta)
        }
    }

    update(delta: number, child: PIXI.Container) {
        super.update(delta, child)
    }

}