import * as PIXI from 'pixi.js'
import { Transformable } from '../extendable'

export class Barrel extends Transformable {
    constructor() {
        super()
        this.assets.push('/images/barrel.png')
    }

    create() {
        return new PIXI.Sprite(PIXI.Texture.from(PIXI.Loader.shared.resources[this.assets[0]].data))
    }

    update(delta: number, child: PIXI.Container) {
        super.update(delta, child)
    }
}