import { useEffect, useRef } from "react";

const useEventListener = <K extends keyof DocumentEventMap>(type: K, handler: Function, el: HTMLElement | any = window) => {
    const savedHandler = useRef<Function>();

    useEffect(() => {
        savedHandler.current = handler;
    }, [handler]);

    useEffect(() => {
        const listener = (e: DocumentEventMap[K]) => savedHandler.current!(e);
        el.addEventListener(type, listener);

        return () => {
            el.removeEventListener(type, listener);
        };
    }, [type, el]);
};

export default useEventListener;
