export const ENTITY_LENGTH = 8              // number of 16 bit unsigned initegers 
export const MAX_ENTITIES = 1024
export const AGREED_UPON_BITS = 16

let stored_upscale_factor = window.localStorage.getItem('NG_UPSCALE_FACTOR')
let upscale_factor_value = 3
if(stored_upscale_factor !== null) {
    upscale_factor_value = Number(stored_upscale_factor)
} else {
    window.localStorage.setItem('NG_UPSCALE_FACTOR', String(upscale_factor_value))
}

if(upscale_factor_value < 1) {
    upscale_factor_value = 1
}

export const UPSCALE_FACTOR = Math.ceil(upscale_factor_value)