import * as PIXI from 'pixi.js'
import { Transformable } from '.'

import animatedTemplate from '../templates/animated'
import {
    ANIMATION_ATTACK,
    ANIMATION_DIE,
    ANIMATION_IDLE,
    ANIMATION_FLEE,
    ANIMATION_WALK
} from '../../constants/'

export class GenericAnimatedSprite extends Transformable {
    sizeX: number = -1
    sizeY: number = -1
    frameLengths: number[] = []
    animationLUT: number[] = []

    constructor() {
        super()
        this.template.load(animatedTemplate)
        this.unpackTemplate()
    }

    create() {
        if (this.assets.length === 0) {
            throw new Error('Your prefab needs to implement an asset')
        } else {
            return this.convertSpriteSheet()
        }
    }

    update(delta: number, child: PIXI.Container) {
        super.update(delta, child)

        let animation = this.attributes.find(o => o.key === 'animation')?.value
        let direction = this.attributes.find(o => o.key === 'direction')?.value
        let hp = this.attributes.find(o=>o.key==='hp')?.value

        const sprites = child.getChildAt(0) as PIXI.Container
        if (animation !== undefined && (this.animationLUT[Number(animation)] < sprites.children.length)) {
            sprites.children.forEach(sprite => sprite.renderable = false)
            const sprite = sprites.children[this.animationLUT[Number(animation)]] as PIXI.AnimatedSprite
            sprite.renderable = true
            if(Number(animation) === ANIMATION_DIE) {
                if(sprite.playing) {
                    sprite.gotoAndPlay(0)
                    sprite.loop = false
                } 
            } else {
                // sprite.gotoAndPlay(0)
            }
        }
        child.children[0].scale.set(direction === 0 ? 1 : -1, 1)
        //@ts-ignore
        child.children[1].children[1].scale.set(Number(hp)/255,1)
        child.children[1].renderable = false
    }

    convertSpriteSheet() {
        const textureAtlas = PIXI.Texture.from(this.assets[0])
        const baseTexture = textureAtlas.castToBaseTexture()

        const _container = new PIXI.Container()

        this.frameLengths.forEach((frameLength, frameIndex) => {
            const textures: PIXI.Texture[] = []
            for (let i = 0; i < frameLength; i++) {
                textures.push(
                    new PIXI.Texture(
                        baseTexture,
                        new PIXI.Rectangle(
                            Math.floor(i * this.sizeX) + 0.0,
                            Math.floor(frameIndex * this.sizeY) + 0.0,
                            this.sizeX,
                            this.sizeY
                        )
                    )
                )
            }
            const sprite = new PIXI.AnimatedSprite(textures)
            sprite.name = `animation_set_${frameIndex}`
            sprite.animationSpeed = 0.15 //1.0 / frameLength
            sprite.gotoAndPlay(Math.floor(Math.random() * frameLength))
            sprite.anchor.set(0.5, 1.0)
            sprite.renderable = false
            _container.addChild(sprite)
        })

        const mainContainer = new PIXI.Container()
        mainContainer.addChild(_container)

        const hpContainer = new PIXI.Container()
        hpContainer.scale.set(.25,1)
        hpContainer.position.set(0, -20)
        const bgSprite = new PIXI.Sprite(PIXI.Texture.from(PIXI.Loader.shared.resources['hp_bar'].data))
        const hpSprite = new PIXI.Sprite(PIXI.Texture.from(PIXI.Loader.shared.resources['hp_bar'].data))

        bgSprite.position.set(-32,0)
        hpSprite.position.set(-32,0)

        hpContainer.addChild(bgSprite)        
        hpContainer.addChild(hpSprite)

        hpSprite.tint = 0xFF0000
        hpSprite.scale.set(1, 1)

        const bgSprite2 = new PIXI.Sprite(PIXI.Texture.from(PIXI.Loader.shared.resources['hp_bar'].data))
        bgSprite2.position.set(-32,2)
        const manaSprite = new PIXI.Sprite(PIXI.Texture.from(PIXI.Loader.shared.resources['hp_bar'].data))
        manaSprite.position.set(-32,2)
        manaSprite.scale.set(1, 1)

        manaSprite.tint = 0x3366FF

        hpContainer.addChild(bgSprite2)
        hpContainer.addChild(manaSprite)

        mainContainer.addChild(hpContainer)

        hpContainer.alpha = 0.5

        return mainContainer
    }
}