import * as PIXI from 'pixi.js'
import type System from '../../Core/System'

export default class DebugViewManager {
    system: System
    debugViewContainer: PIXI.Container = new PIXI.Container()
    constructor (system: System) {
        this.system = system        
    }
    initialize () {
        const c = this.debugViewContainer
        c.renderable = false

        c.position.set(20, 20)

        let screenW = this.system.renderManager.layerManager.dimensions.x
        let screenH = this.system.renderManager.layerManager.dimensions.y
        
        let runningX = 0
        let runningY = 0

        const padding = 2
        const gutter = 10
        const scale = 0.75

        c.scale.set(scale)

        const customSettings = {
            'lights-compute-x': { scale: { x: 1, y: 10 }, shift: 0 },
            'lights-compute-y': { scale: { x: 10, y: 1 }, shift: undefined },
            'lights-compute-reducer': { scale: { x: 1, y: 10 }, shift: 0 },
        }

        this.system.renderManager.textureManager.renderTargets.forEach((renderTarget, name)=>{
            const w = renderTarget.width
            const h = renderTarget.height 
            console.log('name', name, w, h)
            const _c = new PIXI.Container()

            //@ts-ignore            
            const customSetting = customSettings[name]
            if(customSetting !== undefined) {
                _c.scale.set(customSetting.scale.x, customSetting.scale.y)
                _c.scale.set(0,0)
            }

            c.addChild(_c)
            _c.position.set(runningX, runningY)
            const g = new PIXI.Graphics()
            g.position.set(-padding,-padding)
            g.beginFill(0x00FF00)
            g.drawRect(0,0,w+(2*padding),h+(2*padding))
            _c.addChild(g)
            _c.addChild(new PIXI.Sprite(renderTarget))

            if(customSetting !== undefined && customSetting.shift !== undefined) {
                runningX += customSetting.shift
            } else {
                runningX += w + gutter
            }
            if((runningX + w + gutter) > (screenW/scale)) {
                runningX = 0
                runningY += h + gutter
            }  
        })

        const spriteLightsX = new PIXI.Sprite(this.system.renderManager.textureManager.renderTargets.get('lights-compute-x'))
        spriteLightsX.position.set(0,-30)
        spriteLightsX.scale.set(1.0/scale, 10.0/scale)
        const spriteLightsY = new PIXI.Sprite(this.system.renderManager.textureManager.renderTargets.get('lights-compute-y'))
        spriteLightsY.position.set(-30,0)
        spriteLightsY.scale.set(10.0/scale, 1.0/scale)
        const spriteLightsXY = new PIXI.Sprite(this.system.renderManager.textureManager.renderTargets.get('lights-compute-reducer'))
        spriteLightsXY.position.set(-30,-30)
        spriteLightsXY.scale.set(15.0/scale)

        c.addChild(spriteLightsX)
        c.addChild(spriteLightsY)
        c.addChild(spriteLightsXY)

        this.system.app.stage.addChild(c)
    }
}