import { useEffect, useState } from "react"

import { CONTROLS, GRAPHIC_LEVELS } from "../../constants"
import { LocalStorageKey } from "../../enums"
import { useLocalStorage } from "../../hooks"
import { IGraphicLevel } from "../../types"

function ControlsView() {
    const [ngUpscaleFactor, setNgUpscaleFactor] = useLocalStorage<number>(LocalStorageKey.NG_UPSCALE_FACTOR, 4)
    const [graphicLevels, setGraphicLevels] = useState<IGraphicLevel[]>([])

    const handleGraphicLevelClick = (graphicLevel: IGraphicLevel) => {
        setNgUpscaleFactor(graphicLevel.value)
        window.location.reload()
    }

    useEffect(() => {
        setGraphicLevels(GRAPHIC_LEVELS)
    }, [setGraphicLevels])

    return <div className='controls'>
        <h1>Controls</h1>
        <h2>&nbsp;</h2>
        {
            CONTROLS.map(o => {
                return (
                    <div key={o.key} className='controls-table-row'>
                        <div className='controls-table-key'>{o.key}</div>
                        <div className='controls-table-value'>{o.value}</div>
                    </div>
                )
            })
        }
        <h5>Graphics Settings</h5>
        <div className='button-container'>
            {
                graphicLevels.map(graphicLevel => {
                    const isSelected = ngUpscaleFactor === graphicLevel.value
                    const divStyle = {
                        borderColor: isSelected ? '#FFD700' : 'white',
                        color: isSelected ? '#FFD700' : 'white'
                    }
                    return (
                        <div
                            key={graphicLevel.key}
                            className='faux-button'
                            style={divStyle}
                            onClick={() => handleGraphicLevelClick(graphicLevel)}
                        >
                            {graphicLevel.key}
                        </div>
                    )
                })
            }
        </div>
        <h6>Note: Changing graphics settings will refresh the page.</h6>
    </div >
}

export default ControlsView
