import * as PIXI from 'pixi.js'
import { AdvancedBloomFilter } from '@pixi/filter-advanced-bloom'
import { UPSCALE_FACTOR } from '../../../constants'

export default function initializeShaderManager(this: any): void {
    console.log('intializing shader manager')
    this.tickUniforms() // everything works better when you do this once, there is no harm in it

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // compositor
    const compositorFilter = new PIXI.Filter(undefined,
        PIXI.Loader.shared.resources.compositor_frag.data,
        this.compositorUniforms)
    compositorFilter.autoFit = false

    this.compositorUniforms.uEntities = this.system.renderManager.textureManager.renderTargets.get('entity') as PIXI.RenderTexture
    this.compositorUniforms.uBackground = this.system.renderManager.textureManager.renderTargets.get('background') as PIXI.RenderTexture
    this.compositorUniforms.uEmission = this.system.renderManager.textureManager.renderTargets.get('emission') as PIXI.RenderTexture
    this.compositorUniforms.uShadow = this.system.renderManager.textureManager.renderTargets.get('shadow') as PIXI.RenderTexture
    this.compositorUniforms.uFog = this.system.renderManager.textureManager.renderTargets.get('fog') as PIXI.RenderTexture
    this.compositorUniforms.uLights = this.system.renderManager.textureManager.renderTargets.get('lights') as PIXI.RenderTexture
    this.compositorUniforms.uDarkness = this.system.renderManager.textureManager.renderTargets.get('darkness') as PIXI.RenderTexture
    this.compositorUniforms.uEmissionMask = this.system.renderManager.textureManager.renderTargets.get('emission-entities-mask') as PIXI.RenderTexture

    const renderScreen = this.system.renderManager.layerManager.compositedLayers.get('render-screen') as PIXI.Container
    renderScreen.filters = [compositorFilter]

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // shadows
    this.groundShadowFilterUniforms.uEntities = this.system.renderManager.textureManager.renderTargets.get('entity') as PIXI.RenderTexture
    this.groundShadowFilterUniforms.textureDimensions.x = this.system.renderManager.textureManager.dimensions.x
    this.groundShadowFilterUniforms.textureDimensions.y = this.system.renderManager.textureManager.dimensions.y
    this.groundShadowFilterUniforms.uLampPosition = this.system.renderManager.textureManager.renderTargets.get('lights-compute-reducer')

    const groundShadowFilter = new PIXI.Filter(undefined, PIXI.Loader.shared.resources.ground_shadow_frag.data, this.groundShadowFilterUniforms)
    groundShadowFilter.autoFit = false

    const easyLightsFilter = new PIXI.Filter(undefined, PIXI.Loader.shared.resources.easy_lights_frag.data, this.groundShadowFilterUniforms)
    easyLightsFilter.autoFit = false

    const shadowLayer = this.system.renderManager.layerManager.effectsLayers.get('ground-shadow')
    if (shadowLayer !== undefined) {
        shadowLayer.filters = [groundShadowFilter]
    }

    const darknessLayer = this.system.renderManager.layerManager.effectsLayers.get('darkness')
    if(darknessLayer !== undefined) {
        darknessLayer.filters = [easyLightsFilter]
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // lights
    this.lightsFilterUniforms.uEmission = this.system.renderManager.textureManager.renderTargets.get('emission') as PIXI.RenderTexture
    this.lightsFilterUniforms.textureDimensions.x = this.system.renderManager.textureManager.dimensions.x
    this.lightsFilterUniforms.textureDimensions.y = this.system.renderManager.textureManager.dimensions.y

    const lightsFilter = new PIXI.Filter(undefined, PIXI.Loader.shared.resources.lights_frag.data, this.lightsFilterUniforms)
    lightsFilter.autoFit = false

    const uniforms = {
        resolution: {
            x: this.system.renderManager.textureManager.dimensions.x,
            y: this.system.renderManager.textureManager.dimensions.y
        }
    }

    const bloomFilter = new AdvancedBloomFilter()
    bloomFilter.brightness = 1.8
    bloomFilter.threshold = 0.2
    bloomFilter.blur = 10
    bloomFilter.quality = 7
    bloomFilter.resolution = 1

    const lensFlareSize = new PIXI.Point(3,3)

    bloomFilter.pixelSize = lensFlareSize

    // @ts-ignore -- TODO, expose the GUI at the system level
    const bloomFolder = window.gui.addFolder('Bloom Settings')
    bloomFolder.open()
    bloomFolder.add(bloomFilter, 'brightness', 0.0, 10.0, 0.1).name('Brightness')
    bloomFolder.add(bloomFilter, 'threshold', 0.0, 1.0, 0.01).name('Threshold')
    bloomFolder.add(bloomFilter, 'blur', 0.0, 64.0).name('Size')
    bloomFolder.add(bloomFilter, 'quality', 0.0, 64.0).name('Quality')
    bloomFolder.add(lensFlareSize, 'x', 1, 16, 1).name('Lens Flare X').onChange((d:any)=>{ lensFlareSize.x = d; bloomFilter.pixelSize = lensFlareSize  })
    bloomFolder.add(lensFlareSize, 'y', 1, 16, 1).name('Lens Flare Y').onChange((d:any)=>{ lensFlareSize.y = d; bloomFilter.pixelSize = lensFlareSize  })

    bloomFilter.autoFit = false

    const lightsLayer = this.system.renderManager.layerManager.effectsLayers.get('lights')
    if (lightsLayer !== undefined) {
        lightsLayer.filters = [lightsFilter, bloomFilter]
        // lightsLayer.filters = [lightsFilter]
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // lights compute shader
    const lightsComputeUniforms = {
        uEmission: this.system.renderManager.textureManager.renderTargets.get('lights') as PIXI.RenderTexture
    }

    // two shaders, one for x, one for y
    // attach the emission texture to the compute uniforms
    const lightsComputeXFilter = new PIXI.Filter(undefined, PIXI.Loader.shared.resources.lights_compute_x_frag.data, lightsComputeUniforms)
    lightsComputeXFilter.autoFit = false
    const lightsComputeYFilter = new PIXI.Filter(undefined, PIXI.Loader.shared.resources.lights_compute_y_frag.data, lightsComputeUniforms)
    lightsComputeYFilter.autoFit = false
    const lightsComputeReducer = new PIXI.Filter(undefined, PIXI.Loader.shared.resources.lights_compute_reducer_frag.data, {
        uLightsX: this.system.renderManager.textureManager.renderTargets.get('lights-compute-x'),
        uLightsY: this.system.renderManager.textureManager.renderTargets.get('lights-compute-y'),
        dimensions: this.system.renderManager.textureManager.dimensions
    })

    const findLightsXLayer = this.system.renderManager.layerManager.computeLayers.get('find-lights-x')
    if (findLightsXLayer !== undefined) {
        findLightsXLayer.filters = [lightsComputeXFilter]
    }

    const findLightsYLayer = this.system.renderManager.layerManager.computeLayers.get('find-lights-y')
    if (findLightsYLayer !== undefined) {
        findLightsYLayer.filters = [lightsComputeYFilter]
    }

    const findLightsReducerLayer = this.system.renderManager.layerManager.computeLayers.get('find-lights-reducer')
    if(findLightsReducerLayer !== undefined) {
        findLightsReducerLayer.filters = [lightsComputeReducer]
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // fog
    const fogFilter = new PIXI.Filter(undefined, PIXI.Loader.shared.resources.fog_frag.data, this.fogUniforms)
    fogFilter.autoFit = false

    const fogLayer = this.system.renderManager.layerManager.effectsLayers.get('fog')
    if (fogLayer !== undefined) {
        fogLayer.filters = [fogFilter]
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // water
    const waterFilter = new PIXI.Filter(undefined, PIXI.Loader.shared.resources.water_frag.data, this.waterUniforms)
    waterFilter.autoFit = false

    this.waterUniforms.uWaterMask = this.system.renderManager.layerManager.waterMask
    this.waterUniforms.uWaterTexture = this.system.renderManager.layerManager.waterTexture
    this.waterUniforms.renderDimensions.x = this.system.renderManager.textureManager.dimensions.x
    this.waterUniforms.renderDimensions.y = this.system.renderManager.textureManager.dimensions.y
    this.waterUniforms.offset.x = this.system.renderManager.layerManager.offset.x * UPSCALE_FACTOR
    this.waterUniforms.offset.y = this.system.renderManager.layerManager.offset.y * UPSCALE_FACTOR

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // background grass
    const grassFilterBackground = new PIXI.Filter(undefined, PIXI.Loader.shared.resources.grass_move_frag.data, this.grassUniformsBackground)
    grassFilterBackground.autoFit = false

    this.grassUniformsBackground.uGrassMask = this.system.renderManager.layerManager.grassMaskTexture
    this.grassUniformsBackground.renderDimensions.x = this.system.renderManager.textureManager.dimensions.x
    this.grassUniformsBackground.renderDimensions.y = this.system.renderManager.textureManager.dimensions.y
    this.grassUniformsBackground.offset.x = this.system.renderManager.layerManager.offset.x * UPSCALE_FACTOR
    this.grassUniformsBackground.offset.y = this.system.renderManager.layerManager.offset.y * UPSCALE_FACTOR

    const backgroundLayer = this.system.renderManager.layerManager.assetLayers.get('background-level')
    if (backgroundLayer !== undefined) {
        backgroundLayer.filters = [grassFilterBackground, waterFilter]
        // backgroundLayer.filters = [grassFilterBackground]
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // building roof grass
    const grassFilterForeground = new PIXI.Filter(undefined, PIXI.Loader.shared.resources.grass_move_frag.data, this.grassUniformsForeground)
    grassFilterForeground.autoFit = false

    this.grassUniformsForeground.uGrassMask = this.system.renderManager.layerManager.grassMaskBuildingTexture
    this.grassUniformsForeground.renderDimensions.x = this.system.renderManager.textureManager.dimensions.x
    this.grassUniformsForeground.renderDimensions.y = this.system.renderManager.textureManager.dimensions.y
    this.grassUniformsForeground.offset.x = this.system.renderManager.layerManager.offset.x * UPSCALE_FACTOR
    this.grassUniformsForeground.offset.y = this.system.renderManager.layerManager.offset.y * UPSCALE_FACTOR

    const entitiesLayer = this.system.renderManager.layerManager.assetLayers.get('entities')
    console.log(entitiesLayer)
    if (entitiesLayer !== undefined) {
        if(entitiesLayer.filters === null) {
            entitiesLayer.filters = []
        }
        entitiesLayer.filters.push(grassFilterForeground)
    }
}