import { GenericAnimatedSprite } from '../extendable'
import killableTemplate from '../templates/killable'

export class Hedgehog extends GenericAnimatedSprite {
    constructor() {
        super()
        this.template.load(killableTemplate)
        this.unpackTemplate()
        this.assets.push('./images/animals/hedgehog.png')
        this.sizeX = 32
        this.sizeY = 32
        this.frameLengths = [4, 4, 4, 3]
    }
}
