import * as PIXI from 'pixi.js'
import { Transformable } from '../extendable'

export class ShaderSlime extends Transformable {
    rgbGlitchUniforms = {
        offset: Math.random() * 0.1
    }

    startTime: number = 0
    speed: number = 0
    tickerFn: any

    constructor() {
        super()
        this.assets.push('./images/slime-sheet.png')
        this.staticOrInstanced = 'instanced'
        this.startTime = Date.now()
        this.speed = Math.random() * 0.1
    }

    create() {
        const updateUniformValues = (delta: number) => {
            // console.log(this.template.unpack().find(o=>o.key==='id')?.value)
            this.rgbGlitchUniforms.offset = 0.1 * Math.sin((this.startTime + Date.now()) * this.speed)
        }
        this.tickerFn = updateUniformValues
        PIXI.Ticker.shared.add(updateUniformValues)
        return this.convertSlime()
    }

    destroy() {
        PIXI.Ticker.shared.remove(this.tickerFn)
    }

    update(delta: number, child: PIXI.Container) {
        super.update(delta, child)
    }

    convertSlime() {

        const rgbGlitchFilter = new PIXI.Filter(undefined, PIXI.Loader.shared.resources.rgb_glitch_frag.data, this.rgbGlitchUniforms)

        const textureAtlas = PIXI.Texture.from(this.assets[0])

        const sizeX = 32
        const sizeY = 26

        const idleTextures: PIXI.Texture[] = []
        for (let i = 0; i < 8; i++) {
            idleTextures.push(
                new PIXI.Texture(
                    textureAtlas.castToBaseTexture(),
                    new PIXI.Rectangle(i * sizeX, 0, sizeX, sizeY)
                )
            )
        }

        const walkingTextures: PIXI.Texture[] = []
        for (let i = 0; i < 8; i++) {
            walkingTextures.push(
                new PIXI.Texture(
                    textureAtlas.castToBaseTexture(),
                    new PIXI.Rectangle(i * sizeX, 24, sizeX, sizeY)
                )
            )
        }

        const _container = new PIXI.Container()
        // _container.filters = [rgbGlitchFilter]

        const sprite = new PIXI.AnimatedSprite(idleTextures)
        sprite.name = 'idle_animation'
        sprite.animationSpeed = 1.2 / 8
        sprite.play()
        sprite.anchor.set(0.5, 1.0)

        const spriteWalking = new PIXI.AnimatedSprite(walkingTextures)
        sprite.name = 'walking_animation'
        spriteWalking.animationSpeed = 1.2 / 8
        spriteWalking.play()
        spriteWalking.anchor.set(0.5, 1.0)

        spriteWalking.renderable = false

        _container.addChild(sprite)
        _container.addChild(spriteWalking)

        return _container
    }
}