// import { Container } from 'pixi.js'
import type { Container } from 'pixi.js'
import Template, { IPropertyValue } from './Template'
import baseTemplate from '../../prefabs/templates/base'

type PrefabStaticOrInstanced = 'static' | 'instanced'

export default abstract class Prefab {
    staticOrInstanced: PrefabStaticOrInstanced = 'static'
    template: Template
    attributes: IPropertyValue[] = []
    assets: string[] = []

    constructor() {
        this.template = new Template()
        this.template.load(baseTemplate)
    }

    unpackTemplate() {
        this.attributes = this.template.unpack()
    }

    abstract create(): Container
    abstract update(delta: number, child: Container): void
}