import React from 'react'
const availableNFTs = ["0023","0059","0067","0082","0102","0104","0113","0118","0134","0159","0161","0179","0180","0183","0206","0213","0224","0228","0256","0268","0272","0288","0307","0309","0338","0342","0361","0362","0374","0391","0393","0430","0445","0450","0452","0455","0468","0477","0484","0495","0497","0498","0502","0524","0563","0582","0593","0602","0620","0628","0687","0699","0700","0702","0705","0721","0723","0737","0760","0765","0779","0791","0829","0835","0865","0882","0886","0899","0909","0917","0924","0929","0934","0962","0977","0983","0989","0996","1009","1022","1049","1055","1056","1060","1061","1103","1140","1169","1201","1210","1211","1215","1218","1219","1236","1243","1244","1249","1278","1281"]

let nftId = window.localStorage.getItem('nft_id') || '0023'

export default class SelectNFT extends React.Component<{}> {

  render() {
    return (
      <div className='select-nft'>
        <select defaultValue={nftId} onChange={(e)=>{ nftId = e.target.value }}>
          {
            availableNFTs.map(nftId=>{
              return (
                <option key={nftId} value={nftId}>{nftId}</option>
              )
            })
          }
        </select>
        <button onClick={(e)=>{
          window.localStorage.setItem('nft_id', nftId)
          window.location.reload()
        }}>Change</button>
      </div>
    )
  }
}
