import type System from '../engine/lib/Core/System'
import * as PIXI from 'pixi.js'
import tiledFixer from './TiledFixer'

export interface TiledLevelObject { objectId: string, gid: string, x: string, y: string, width: string, height: string, rotation: string | null, gidErrorFlag: string }

export class TiledManager {
    system: System
    constructor(system: System) {
        this.system = system
    }

    parseAssets(xmlDocument: XMLDocument): Map<string, string> {
        const gids = new Map<string, string>()  // these are the object GIDs
        const tilesets = xmlDocument.getElementsByTagName('tileset')

        for (let i = 0; i < tilesets.length; i++) {
            const tileset = tilesets[i]
            const firstgid = tileset.getAttribute('firstgid')
            for (let j = 0; j < tileset.children.length; j++) {
                const child = tileset.children[j]
                const combined = String(Number(firstgid) + Number(child.id))
                const src = child.querySelector('image')?.getAttribute('source') as string
                if (src !== undefined) {
                    gids.set(combined, src)
                }
            }
        }
        return gids
    }

    parseObjectGroups(xmlDocument: XMLDocument): Map<string, TiledLevelObject> {
        const levelObjects = new Map<string, TiledLevelObject>()

        const groups = xmlDocument.getElementsByTagName('objectgroup')

        const validGroups = ['15', '8', '6', '16']

        for (let i = 0; i < groups.length; i++) {
            const group = groups[i]
            // console.log(group.id, 'group id')
            if (validGroups.indexOf(String(group.id)) !== -1) {
                const groupLength = group.children.length
                // console.log('adding level objects for group ', group.id)
                for (let j = 0; j < groupLength; j++) {
                    const gameObject = group.children[j]

                    const gid = gameObject.getAttribute('gid') as string
                    const objectId = gameObject.getAttribute('id') as string
                    const x = gameObject.getAttribute('x') as string
                    const y = gameObject.getAttribute('y') as string
                    const width = gameObject.getAttribute('width') as string
                    const height = gameObject.getAttribute('height') as string
                    const rotation = gameObject.getAttribute('rotation') as string

                    const fixedGID = this.fixTiledBugs(gid).value
                    const gidErrorFlag = this.fixTiledBugs(gid).flag

                    // console.log({
                    //     gid: fixedGID, objectId, x, y, width, height, rotation, gidErrorFlag
                    // })

                    levelObjects.set(objectId, {
                        gid: fixedGID, objectId, x, y, width, height, rotation, gidErrorFlag
                    })
                }
                // console.log(levelObjects.size)
            }
        }
        return levelObjects
    }

    fixTiledBugs(gid: string) {
        let gidErrorFlag = ''
        let gidNumber = Number(gid)
        if (gidNumber > Math.pow(2, 30)) {
            gidErrorFlag = (BigInt(gidNumber) & BigInt(0xF0000000)).toString(16).charAt(0)
            gidNumber = gidNumber & ((1 << 12) - 1)
        }
        return { flag: gidErrorFlag, value: String(gidNumber) }
    }

    initialize(callback: any) {
        const level = PIXI.Loader.shared.resources.tiled_level.data as XMLDocument

        const gids = this.parseAssets(level)

        const levelObjects = this.parseObjectGroups(level)

        // find all the unique assets and download them
        const assets: String[] = []
        gids.forEach((asset, gid) => {
            const assetURL = `./images/Tiled_village/${asset}`
            if (assets.indexOf(assetURL) === -1) {
                assets.push(assetURL)
            }
        })

        assets.forEach((asset: String) => {
            PIXI.Loader.shared.add(String(asset))
        })
        PIXI.Loader.shared.load(() => {

            const loadingScreen = document.querySelector('.loading') as HTMLElement
            loadingScreen.style.display = 'none'

            const entities = this.system.renderManager.layerManager.assetLayers.get('entities')
            const overlay = this.system.renderManager.layerManager.assetLayers.get('overlay')

            levelObjects.forEach((levelobject, objectId) => {
                const assetURL = gids.get(levelobject.gid as string) as string

                // const image = PIXI.Loader.shared.resources[`./images/Tiled_village/${assetURL}`].data

                // const sprite = new PIXI.Sprite(PIXI.Texture.from(image))
                const sprite = tiledFixer(assetURL, levelobject)

                sprite.interactive = false
                sprite.interactiveChildren = false
                sprite.anchor.set(0, 1)

                const x = Math.floor(Number(levelobject.x))
                const y = Math.floor(Number(levelobject.y))
                sprite.name = levelobject.objectId
                sprite.position.set(x, y)

                // Tiled has the stupidest way of handling rotation, thus we suffer
                if (levelobject.gidErrorFlag !== '') {
                    if (levelobject.gidErrorFlag == '8') {
                        sprite.scale.set(-1, 1)
                        sprite.anchor.set(1, 1)
                    }
                    if (levelobject.gidErrorFlag == '4') {
                        // todo I am not sure this is the way to handle this
                        sprite.scale.set(-1, 1)
                        sprite.anchor.set(1, 1)
                        sprite.position.x -= Math.round(sprite.width)
                        sprite.position.y += Math.round(sprite.height)
                    }
                    if (levelobject.gidErrorFlag == 'c') {
                        // todo I know there is supposed to be something going on here
                        // sprite.scale.set(-1, 1)
                        // sprite.anchor.set(0, 0)
                    }
                }
                entities?.addChild(sprite)
            })

            callback()
        })


    }
}

