import * as PIXI from 'pixi.js'
import { Transformable } from '../extendable'

export class Lightning extends Transformable {
    uniforms = {
        time: Math.random() * 10240.0,
    }

    tickerFn: any

    graphics: PIXI.Graphics[] = []
    visibleIndex: number = 0

    constructor() {
        super()
        this.assets.push('./images/bridge.png')
        this.staticOrInstanced = 'instanced'
    }

    create() {
        const updateUniformValues = (delta: number) => {
            this.uniforms.time += delta
            this.visibleIndex += 1
            this.visibleIndex = this.visibleIndex % this.graphics.length
            if (Math.random() < 0.35) return
            this.graphics.forEach(g => { g.renderable = false })
            if (Math.random() < 0.5) return
            this.graphics[this.visibleIndex].renderable = true
        }
        this.tickerFn = updateUniformValues
        PIXI.Ticker.shared.add(updateUniformValues)
        return this.createAsset()
    }

    destroy() {
        PIXI.Ticker.shared.remove(this.tickerFn)
    }

    update(delta: number, child: PIXI.Container) {
        super.update(delta, child)
    }

    createAsset() {
        const _container = new PIXI.Container()
        for (let i = 0; i < 16; i++) {
            _container.addChild(this.makeLightning())
        }
        _container.children.forEach(child => { this.graphics.push(child as PIXI.Graphics) })
        return _container
    }

    makeLightning(): PIXI.Graphics {
        const w = 96
        const h = 256

        const g = new PIXI.Graphics()
        g.beginFill(0xFFFFFF)

        let runningX = Math.floor(w / 2)
        let runningY = h
        let runningDirection = 1

        const lightningData = new Array(64).fill(0).map((o, i) => {
            return {
                slope: Math.floor(Math.random() * 4) + 2,
                distance: Math.floor(Math.random() * 12) + 4
            }
        })

        lightningData.forEach((d, i) => {
            runningDirection *= -1
            if (runningY < 0) return
            for (let i = 0; i < d.distance; i++) {
                runningY -= 1
                runningX += runningDirection * d.slope
                if (runningX >= w || runningX <= 0) {
                    runningDirection *= -1
                }
                if (runningY > 0) {
                    g.drawRect(runningX, runningY, d.slope, 1)
                }
                if (Math.random() < 0.2) {          // do a branch?
                    const previousRunningY = runningY
                    const branchDistance = d.distance * 0.5
                    const branchDirection = runningDirection * -1
                    const tempSlope = Math.floor(Math.random() * 2) + 2
                    let currentRunningX = runningX
                    for (let j = 0; j < branchDistance; j++) {
                        runningY += 1
                        currentRunningX += branchDirection * tempSlope
                        if (runningY > 0) {
                            g.drawRect(currentRunningX, runningY, tempSlope, 1)
                        }        
                    }
                    runningY = previousRunningY     // reset the running Y position
                }
            }
        })

        g.position.set(-w * 0.5, -h)
        return g
    }
}

