import { ENTITY_LENGTH, MAX_ENTITIES, SIZEOF_INT16 } from '../../constants'
import UpdatedEntitiesManager from './UpdatedEntitiesManager'
import Prefab from '../Prefab/Prefab'

const spawnPoint = { x: 1630, y: 850 }

export default class World {
    // ECS state
    private _buffer: ArrayBuffer;
    _view: Uint16Array;
    _updatedEntitiesManager: UpdatedEntitiesManager;
    disconnectedPCs: number[] = []

    identity = { id: -1, address: -1 }

    constructor() {
        this._buffer = new ArrayBuffer(ENTITY_LENGTH * MAX_ENTITIES * SIZEOF_INT16)
        this._view = new Uint16Array(this._buffer)
        this._updatedEntitiesManager = new UpdatedEntitiesManager()

        // solo mode hack for demo
        // todo - remove or make programmable
        this.identity.address = 0        
        this.write(0,8,[128,4000,spawnPoint.x,spawnPoint.y,0,0,0,0])
        this.write(8,8,[129,7920,spawnPoint.x,spawnPoint.y-20,0,8,0,0])        

    }

    write(address: number, length: number, bytes: number[]) {
        if (length === 0) {
            return
        }
        let i = 0
        for (i = 0; i < length; i++) {
            this._view[i + address] = bytes[i]
        }
        if (length > ENTITY_LENGTH) {
            for (i = 0; i < Math.ceil(length / ENTITY_LENGTH); i++) {
                this._updatedEntitiesManager.add(Math.floor(address / ENTITY_LENGTH) + i)
            }
        } else {
            this._updatedEntitiesManager.add(Math.floor(address / ENTITY_LENGTH))
        }
    }

    setIdentity(identity: { id: number, address: number }) {
        this.identity = identity
    }

    loadEntityBytesIntoPrefab(index: number, prefab: Prefab) {
        prefab.template.packRaw(this._view.slice(index * ENTITY_LENGTH, (index * ENTITY_LENGTH) + ENTITY_LENGTH))
        prefab.unpackTemplate()
    }
}
